import React from 'react';
import { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import axios from 'axios';


import Counter from '../../../Components/Counter';

const Container = styled.div`
  animation: appear 0.5s forwards;
`

const Top = styled.div`
  display: flex;
  align-items: center;
  h1 {
    flex: 0;
    font-size: 5rem;
    margin: 0rem;
  }
`
const Content = styled.div`
  width: 100%;

  .counters, 
  .buttons{
    display: flex;
    justify-content: space-between;
    gap: 1rem;
    margin-top: 2rem;
    .counter, 
    .button {
      flex: 1;
    }
    .button {
      cursor: pointer;
    }
  }
`

const AdminButton = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #F8F8F8;
  margin: 5rem;
  padding: 2rem;
  border-radius: 5px;
  cursor: pointer;
  img {
    height:  auto;
    width: 16rem;
  }
  .caption {
    margin: 2rem;
    text-align: center;
  }
`

const AdminOvButton = ({type, caption}) => {
  return (
    <AdminButton>
      {type === 'projects' && <img src="images/icons/layers.png" />}
      {type === 'ideas' && <img src="images/icons/bulb.svg" />}
      {type === 'reports' && <img src="images/icons/report.png" />}
      {type === 'users' && <img src="images/icons/user.svg" />}
      <h1 className="caption">{caption}</h1>
    </AdminButton>
  )
}

const AdminOverview = ({setTab}) => {

  const [usersCounter, setUsersCounter] = useState(0);
  const [projectsCounter, setProjectsCounter] = useState(0);
  const [activeUsersCounter, setActiveUsersCounter] = useState(0);
  const [projectUsersCounter, setProjectUsersCounter] = useState(0);

  useEffect(() => {
    let users = axios.get(`${process.env.REACT_APP_API_URL}api/users/count`);
    let projects = axios.get(`${process.env.REACT_APP_API_URL}api/projects/count`);
    let activeUsers = axios.get(`${process.env.REACT_APP_API_URL}api/users/count-active`);
    Promise.all([users, projects, activeUsers])
      .then(([users, projects, activeUsers]) => {
        let projectsPerUsers = Math.ceil(projects.data[0].count / users.data[0].count);
        console.log(projectsPerUsers);
        setUsersCounter(users.data[0].count);
        setProjectsCounter(projects.data[0].count);
        setActiveUsersCounter(activeUsers.data[0].count);
        setProjectUsersCounter(projectsPerUsers);
      })
  }, []);

  return (
    <Container>
      <Top>
        <h1>Overview</h1>
      </Top>
      <Content>
        <div className="counters">
          <div className="counter"><Counter value={usersCounter} caption="Usuários Cadastrados"/></div>
          <div className="counter"><Counter value={activeUsersCounter} caption="Usuários Ativos"/></div>
          <div className="counter"><Counter value={projectsCounter} caption="Projetos Cadastrados"/></div>
          <div className="counter"><Counter value={projectUsersCounter} caption="Projetos Por Pessoa"/></div>
        </div>
        <div className="buttons">
          <div className="button" onClick={() => setTab('ideas')}><AdminOvButton type="ideas" caption="Gerenciar Ideias" /></div>
          <div className="button" onClick={() => setTab('projects')}><AdminOvButton type="projects" caption="Gerenciar Projetos"/></div>
          <div className="button" onClick={() => setTab('reports')}><AdminOvButton type="reports" caption="Gerenciar Relatórios"/></div>
          <div className="button" onClick={() => setTab('users')}><AdminOvButton type="users" caption="Gerenciar Usuários"/></div>
        </div>
      </Content>
    </Container>
  )

}

export default connect()(AdminOverview);