import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

import { connect } from 'react-redux';
import axios from 'axios';

import styled from 'styled-components';
import Button from '../Components/Button';
import Colors from '../Components/Colors';
import TextInput from '../Components/TextInput';

import {ReactComponent as CheckCircle} from '../Components/icons/check-circle_1.svg';

const Container = styled.main`
  height: 100vh;
  width: 100vw;
  padding: 0px;
  display: flex;
  flex-direction: row;
  background: url('images/login_background.jpg');
  background-repeat: no-repeat;
  background-size: cover;
`

const Overlay = styled.div`
  height: 100vh;
  width: 100vw;
  padding: 0px;
  display: flex;
  flex-direction: row;
  background: rgba(255,255,255, 0.9);
  
`

const SignIn = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .title {
    width: 50%;
    font-size: 4.6rem;
    margin-bottom: 1rem;
  }
`

const SignUp = styled.section`
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .title {
    width: 50%;
    font-size: 4.6rem;
    margin-bottom: 1rem;
  }
`

const Card = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  background-color: ${props => props.bg};
  padding: 2rem;
  width: 47%;
  height: 40%;
  .login-card {
    height: 100%;
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    a{
      font-size: 1.4rem;
    }
  }
`

const Topic = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  svg {
    height: 5rem;
    margin-right: 1rem;
  }
  p {
    font-size: 1.6rem;
  }
`




const Login = ({setUser, showAlert, Loading}) =>  {

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  useEffect(() => {
    checkLogedInUser()
  }, [])

  const checkLogedInUser = async () => {
    let isLogedIn = await localStorage.getItem('isLogedIn');
    if(isLogedIn) {
      await axios.get(`${process.env.REACT_APP_API_URL}api/users/getData`, {
        headers: {
          token: isLogedIn
        }
      })
        .then(res => {
          setUser(res.data);
        })
        .catch(err => {
          console.log(err);
        })
    }
    Loading.hide();
  }

  const handleSignInKeyUp = evt => {
    if(evt.which === 13){
      handleSignInClick();
    }
  }

  const handleSignInClick = () => {
    if(email === '') return showAlert({
      title: 'Email Inválido',
      message: 'Por favor informe um email válido'
    });
    if(password === '') return showAlert({
      title: 'Senha Inválida',
      message: 'Por favor informe a senha'
    });
    Loading.show()
    axios.post(`${process.env.REACT_APP_API_URL}api/users/login`, {email: email, password: password})
      .then(res => {
        const user = res.data;
        localStorage.setItem('isLogedIn', user.token);
        setUser(user);
        Loading.hide();
      })
      .catch(err => {
        // console.log(err.response)
        Loading.hide();
        if (err.response.status === 403) {
          showAlert({
            title: 'Falha no login',
            message: 'Login e/ou senha inválidos, por favor verifique as credenciais e tente novamente'
          })
        }
        else{
          showAlert({
            title: 'Falha no login',
            message: `Erro ${err.response.status}`
          })
        }
      })
  }

  const handleSignUpClick = () => {
    
  }

  return (
    <Container>
      <Overlay>
        <SignIn>
          <h1 className="title">Login</h1>
          <Card bg={Colors.gray} onKeyUp={handleSignInKeyUp}>
            <div className="login-card">
              <TextInput name="email" label="Digite seu e-mail" type="email" value={email} onChange={setEmail}/>
              <TextInput name="password" label="Digite sua senha" type="password" value={password} onChange={setPassword}/>
              <Link to="/forgotpassword">Esqueci minha senha</Link>
              <Button caption="Entrar" color={Colors.green} onClick={handleSignInClick}/>
            </div>
          </Card>
        </SignIn>
        <SignUp>
          <h2 className="title">Ainda não assinou?</h2>
          <Card bg={Colors.gray}>
            <div className="login-card">
              <Topic>
                <CheckCircle fill={Colors.green} />
                <p>Centenas de ideias de negócios detalhadas para você se inspirar a criar sua própria empresa</p>
              </Topic>
              <Topic>
                <CheckCircle fill={Colors.green} />
                <p>Uma ferramenta inovadora desenhada para te dar confiança ao tirar suas ideias do papel</p>
              </Topic>
              <Topic>
                <CheckCircle fill={Colors.green} />
                <p>Relatórios sobre o mercado com cobertura de novas tendências e dicas jurídicas e contábeis.</p>
              </Topic>
              <Button caption="Assinar Agora" color={Colors.orange} onClick={handleSignUpClick}/>
            </div>
          </Card>
        </SignUp>
      </Overlay>
    </Container>
  )
}

const mapStateToProps = (state) => ({ 

});
const mapDispatchToProps = (dispatch) => ({
  setUser: (data) => {
    dispatch({type: 'LOGIN_USER', ...data})
  },
  showAlert: ({title, message, callback = () => {}}) => dispatch({type: 'SHOW_ALERT', title: title, message: message, callback: callback}),
  Loading: {
    show: () => dispatch({type: 'SHOW_LOADING'}),
    hide: () => dispatch({type: 'HIDE_LOADING'}),
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(Login);