import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import ReactTooltip from 'react-tooltip';
import { connect } from 'react-redux';
import axios from 'axios';

import Select from '../../Components/Select';
import Button from '../../Components/Button';

import { ReactComponent as Like } from '../../Components/icons/like.svg'
import { ReactComponent as Star } from '../../Components/icons/star-outline.svg'
import { ReactComponent as StarFilled } from '../../Components/icons/star-full-outline.svg'
import { ReactComponent as Idea } from '../../Components/icons/idea.svg'
import Colors from '../../Components/Colors';
import Modal from '../../Components/Modal';
import TextInput from '../../Components/TextInput';

const Container = styled.div`
  width: auto; 
  margin: 5rem 10rem;
  a{
    text-decoration: none;
  }
  overflow: hidden;
`

const Top = styled.div`
  h1 {
      font-size: 6rem;
      margin: 0rem;
    }
    p{
      font-size: 3.6rem;
      margin: 0.8rem 0rem;
    }
`

const Tabs = styled.div`
  display: flex;
  margin-bottom: 3rem;
  .tab {
    cursor: pointer;
    color: rgba(0,0,0,0.3);
    &:first-child {
      padding-right: 2rem;
      border-right: 4px solid #FFBD59;
    }
    &:last-child {
      padding-left: 2rem;
    }
    &.selected {
      color: black;
    }
  }
`

const Filters = styled.div`
  display: flex;
  justify-content: space-between;
  .filter {
    flex: 1;
    max-width: 18%

  }

`

const NewIdea = styled.div`
  background: #F8F8F8;
  display: flex;
  align-items: center;
  padding: 2rem;
  border-radius: 10px;
  margin-bottom: 2rem;
  .icon {
    flex: 0;
    margin: 1rem; 
    svg {
      height: 13rem;
      fill: url(#MyGradient);
    }
  }
  .text {
    flex: 1;
    margin-left: 5rem;
    h1 {
      font-family: Montserrat-Black;
      font-size: 3rem;
    }
    p {
      font-family: Montserrat-Regular;
      font-size: 2rem;
    }
  }
  .button {
    flex: 0.4;
  }
`

const Table = styled.div`
  width: 100%;
  overflow-y: auto;
  max-height: ${props => props.tab === 'list' ? 70 : 50}vh;
  @media screen and (max-width: 1366px){
    max-height: ${props => props.tab === 'list' ? 65 : 40}vh;
  }
  table {
    width: 100%;
    border-spacing: 0px;
    transform-style: preserve-3d;
    thead {
      font-family: Montserrat-Black;
      transform: translate3d(0, 0, 1px);
      tr {
        height: 10rem;
        th {
          position: sticky;
          top: 0;
          background-color: white;
          min-width: 10%;
          min-height: 5rem;
          font-size: 2rem;
          border-bottom: 3px solid black;
        }
      }
    }
    tbody {
      font-family: Montserrat-Bold;
      transform: translate3d(0, 0, 0);
      tr{
        cursor: pointer;
        background-color: white;
        transition: background-color 0.3s ease;
        td{
          text-align: center;
          font-size: 1.8rem;
          max-width: 30rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          height: 5.7rem;
          svg {
            width: 3rem;
            padding: 1rem;
            &.thumbs-down {
              transform: rotate(180deg)
            }
          }
          &:first-child{
            font-family: Montserrat-Black;
          }
        }
        &:hover {
          background-color: #DDD;
        }
      }
    }
  }
`

const ModalForm = styled.div`
  display: flex;
  flex-direction: column;
  .top {
    display: flex;
    justify-content: space-between;
    .title {
      font-size: 3.6rem;
      font-family: Montserrat-Black;
    }
    .close {
      font-size: 2rem;
      font-family: Montserrat-Medium;
      padding: 0rem 2rem 2rem 0rem;
      cursor: pointer;
    }
    svg {
      width: 3rem;
      padding: 1rem;
      cursor: pointer;
      &.thumbs-down {
        transform: rotate(180deg)
      }
    }
  }
  .content {
    display: flex;
    .left {
      flex: 0.8;
    }
    .right {
      flex: 1;
      display: flex;
      flex-direction: column;
    }
    .input {
      margin: 1rem;
    }
    .textarea {
      margin: 1rem;
      height: 15rem;
    }
    .buttons {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      margin: 1rem;
      /* margin-left: 15rem;
      @media screen and (max-width: 1366px){
        margin-left: 10rem;
      } */
      .custom-button {
        width: fit-content;
        margin-left: 1rem;
        max-height: 5rem;
      }
    }
  }
`

const Ideas  = ({iniTab  = 'list', setMenu, User, showAlert, showConfirm, loading}) => {

  const getAllIdeas = async () => {
    let response = await axios.get(dataListUrl);
    return response.data;
    
  }

  const setores = [
    {value: 'Financeiro', caption: 'Financeiro'},
    {value: 'Saúde e Fitness', caption: 'Saúde e Fitness'},
    {value: 'Educação', caption: 'Educação'},
    {value: 'Varejo', caption: 'Varejo'},
    {value: 'Recursos Humanos', caption: 'Recursos Humanos'},
    {value: 'Serviços Empresariais', caption: 'Serviços Empresariais'},
    {value: 'Marketing', caption: 'Marketing'},
    {value: 'Mobilidade e Transportes', caption: 'Mobilidade e Transportes'},
    {value: 'Alimentação', caption: 'Alimentação'},
    {value: 'Logística', caption: 'Logística'},
    {value: 'Construção Civil', caption: 'Construção Civil'},
    {value: 'Esportes', caption: 'Esportes'},
    {value: 'Agricultura e Pecuária', caption: 'Agricultura e Pecuária'},
    {value: 'Eventos', caption: 'Eventos'},
    {value: 'TI', caption: 'TI'},
    {value: 'Governo', caption: 'Governo'},
    {value: 'Serviços Residenciais', caption: 'Serviços Residenciais'},
    {value: 'Turismo', caption: 'Turismo'},
    {value: 'PET', caption: 'PET'},
    {value: 'Jurídico', caption: 'Jurídico'},
    {value: 'Beleza e Cosméticos', caption: 'Beleza e Cosméticos'},
    {value: 'Energia e Manejo de Resíduos', caption: 'Energia e Manejo de Resíduos'},
    {value: 'Música e Artes', caption: 'Música e Artes'},
    {value: 'Relacionamentos', caption: 'Relacionamentos'},
    {value: 'Imobiliário', caption: 'Imobiliário'}
  ];

  const classificar = [
    {value: 'conhecimento', caption: 'Conhecimentos Específicos'},
    {value: 'tecnologia', caption: 'Tecnologia Necessária'},
    {value: 'investimento', caption: 'Investimento Inicial'},
  ]

  const conhecimentos = [
    {value: 1, caption: 'Muito Baixa'},
    {value: 2, caption: 'Baixa'},
    {value: 3, caption: 'Média'},
    {value: 4, caption: 'Alta'},
    {value: 5, caption: 'Muito Alta'}
  ]
  const tecnologias = [
    {value: 1, caption: 'Muito Baixa'},
    {value: 2, caption: 'Baixa'},
    {value: 3, caption: 'Média'},
    {value: 4, caption: 'Alta'},
    {value: 5, caption: 'Muito Alta'}
  ]

  const newFormDefault = {
    setor: '',
    modelo: '',
    conhecimento: '',
    tecnologia: '',
    investimento: '',
    description: ''
  }
  

  // STATES
  const [tab, setTab] = useState(iniTab);
  const [dataListUrl, setDataListUrl] = useState(`${process.env.REACT_APP_API_URL}api/ideas/${User.id}`);
  const [ideas, setIdeas] = useState([]);
  const [conhecimento, setConhecimento] = useState('');
  const [tecnologia, setTecnologia] = useState('');
  const [setor, setSetor] = useState('');
  const [sortBy, setSortBy] = useState('id');
  const [itemModal, setItemModal] = useState(false);
  const [newItemModal, setNewItemModal] = useState(false);
  const [formItem, setFormItem] = useState({});
  const [formNewItem, setFormNewItem] = useState({
    nome: '',
    setor: '',
    modelo: '',
    conhecimento: '',
    tecnologia: '',
    investimento: '',
    description: ''
  });
  
  useEffect(() => {
    ReactTooltip.rebuild()
  })

  useEffect(() => {
    setMenu('ideas');

    axios.get(dataListUrl)
      .then(response => {
        let filtered = response.data.filter(idea => 
          (idea.setor === setor || setor === '') &&
          (idea.conhecimento === conhecimento || conhecimento === '') &&
          (idea.tecnologia === tecnologia || tecnologia === '')
        );
        filtered.sort((a, b) => a[sortBy] - b[sortBy]);
        setIdeas(filtered);
      })
    
  }, [])

  const fieldInvestimento = (val) => {
    switch (val) {
      case 1:
        return '$';
        break;
      case 2:
        return '$$';
        break;
      case 3:
        return '$$$';
        break;
      case 4:
        return '$$$$';
        break;
      case 5:
        return '$$$$$';
        break;
    }
  }
  const fieldIntensity = (val) => {
    switch (val) {
      case 1:
        return 'Muito Baixa';
        break;
      case 2:
        return 'Baixa';
        break;
      case 3:
        return 'Média';
        break;
      case 4:
        return 'Alta';
        break;
      case 5:
        return 'Muito Alta';
        break;
    }
  }

  const refresh = () => {
    loading(true);
    console.log('iniciando busca');
    axios.get(dataListUrl)
      .then(response => { 
        let filtered = response.data.filter(idea => 
          (idea.setor === setor || setor === '') &&
          (idea.conhecimento === conhecimento || conhecimento === '') &&
          (idea.tecnologia === tecnologia || tecnologia === '')
        );
        filtered.sort((a, b) => a[sortBy] - b[sortBy]);
        setIdeas(filtered);
        console.log('busca finalizada');
        loading(false);
      })
  }

  const handleTabClick = async (newtab) => {
    setTab(newtab);
  }
  useEffect(() => {
    if(tab === 'list'){
      setDataListUrl(`${process.env.REACT_APP_API_URL}api/ideas/${User.id}`)
    }
    if(tab === 'mine'){
      setDataListUrl(`${process.env.REACT_APP_API_URL}api/ideas/favorites/${User.id}`)
    }
  }, [tab])
  useEffect(refresh , [dataListUrl])


  const handleIdeaClick = evt => {
    setFormItem({index: evt.currentTarget.id, ...ideas[evt.currentTarget.id]});
  }
  useEffect(() => {
    if(formItem.id) setItemModal(true)
    else setItemModal(false)
  }, [formItem]);

  const handleFilters = async () => {
    let data = await getAllIdeas();
    let filtered = data.filter(idea => 
      (idea.setor === setor || setor === '') &&
      (idea.conhecimento === conhecimento || conhecimento === '') &&
      (idea.tecnologia === tecnologia || tecnologia === '')
    );
    filtered.sort((a, b) => a[sortBy] - b[sortBy]);
    setIdeas(filtered);
    if(formItem.index) setFormItem({...formItem, ...filtered[formItem.index]});
  }
  useEffect(handleFilters, [setor, conhecimento, tecnologia, sortBy]);

  const handleLikeClick = async (evt) => {
    evt.stopPropagation();
    await axios.post(`${process.env.REACT_APP_API_URL}api/ideas/like`, {user_id: User.id, idea_id: evt.currentTarget.dataset.idea}) ;
    handleFilters();
  }
  const handleDislikeClick = async (evt) => {
    evt.stopPropagation();
    await axios.post(`${process.env.REACT_APP_API_URL}api/ideas/dislike`, {user_id: User.id, idea_id: evt.currentTarget.dataset.idea}) ;
    handleFilters();
  }
  const handleFavoriteClick = async (evt) => {
    evt.stopPropagation();
    await axios.post(`${process.env.REACT_APP_API_URL}api/ideas/favorite  `, {user_id: User.id, idea_id: evt.currentTarget.dataset.idea}) ;    handleFilters();
  }

  const handleSaveNewIdea = async () => {
    let error = Object.keys(formNewItem).some(item => formNewItem[item] === '')
    if (error) return showAlert({title: 'Preenchimento Incorreto', message: 'Todos os campos são obrigatórios'});
    formNewItem.owner = User.id;
    let response = await axios.post(`${process.env.REACT_APP_API_URL}api/ideas/create`, {data: formNewItem});
    if(response.status === 200){
      showAlert({
        title: 'Ideia Adicionada',
        message: 'Sua idéia foi adicionada a lista "Minhas Ideias"',
        callback: () => {
          setNewItemModal(false)
          setFormNewItem(newFormDefault);
          refresh();
        }
      })
    }
  }

  const handleDeleteIdea = async (ideaId) => {
    showConfirm({
      title: "Excluir Ideia",
      message: "Deseja realmente ecluir esta ideia? (Esta ação é irreversível)",
      onYes: async () => {
        let response = await axios.get(`${process.env.REACT_APP_API_URL}api/ideas/delete/${ideaId}`);
        if(response.status === 200){
          showAlert({
            title: 'Ideia Excluída',
            message: 'Sua idéia foi Excluída',
            callback: () => {
              setFormItem({});
              refresh();
            }
          })
        }
      }
    })
  }

  const handleStartProject = (ideaId) => {
    showConfirm({
      title: 'Iniciar projeto',
      message: "Deseja realmente iniciar um projeto para esta ideia?",
      onYes: async () => {
        let response = await axios.post(`${process.env.REACT_APP_API_URL}api/projects/create/`, {user: User.id, idea: ideaId});
        if(response.status === 200){
          showAlert({
            title: 'Projeto Iniciado',
            message: 'O projeto foi iniciado, você pode vê-lo na aba Projetos.',
            callback: () => {
              setFormItem({});
              refresh();
            }
          })
        }
      }
    })
  }

  return(
    <Container>
      <Modal visible={itemModal}>
        <ModalForm>
          <div className="top">
            <h1 className="title">{formItem.nome || `Ideia ${formItem.id}`} </h1>
            {tab === 'list' &&
              <div className="actions">
                <Like onClick={handleLikeClick} data-idea={formItem.id} data-form={true} fill={formItem.like === 1 ? Colors.green : '#AAA'} />
                <Like onClick={handleDislikeClick} data-idea={formItem.id} data-form={true} fill={formItem.like === 0 ? "red" : '#AAA'} className="thumbs-down" />
                {formItem.favorite === null ?
                  <Star onClick={handleFavoriteClick} data-idea={formItem.id} data-form={true} fill={Colors.orange} />
                :
                  <StarFilled onClick={handleFavoriteClick} data-idea={formItem.id} data-form={true} fill={Colors.orange} />
                }
              </div>
            }
            <h3 className="close noselect" onClick={() => setFormItem({})}>X</h3>
          </div>
          <div className="content">
            <div className="left">
              <div className="input">
                <TextInput size={1.3} label='Setor' value={formItem.setor} disabled={true}/>
              </div>
              <div className="input">
                <TextInput size={1.3} label='Modelo de Receita' value={'Transacional'} disabled={true}/>
              </div>
              <div className="input">
                <TextInput size={1.3} label='Conhecimentos Específicos' value={fieldIntensity(formItem.conhecimento)} disabled={true}/>
              </div>
              <div className="input">
                <TextInput size={1.3} label='Tecnologia Necessária' value={fieldIntensity(formItem.tecnologia)} disabled={true}/>
              </div>
            </div>
            <div className="right">
              <div className="input">
                <TextInput size={1.3} label='Investimento Inicial' value={`${fieldIntensity(formItem.tecnologia)} (${fieldInvestimento(formItem.investimento)})`} disabled={true}/>
              </div>
              <div className="textarea">
                <TextInput size={1.3} label='Descrição' value={formItem.description} disabled={true} multiline={true}/>
              </div>
              <div className="buttons">
                {formItem.owner && 
                  <div className="delete"><Button caption="Excluir" color="red" onClick={() => handleDeleteIdea(formItem.id)}/></div>
                }
                <Button color={Colors.orange} caption="Ver Relatórios"/>
                <Button color={Colors.green} caption="Iniciar Projeto" onClick={() => handleStartProject(formItem.id)} />
              </div>
            </div>
          </div>
        </ModalForm>
      </Modal> 
      <Modal visible={newItemModal}>
        <ModalForm>
          <div className="top">
            <h1 className="title">Nova Ideia</h1>
            <h3 className="close noselect" onClick={() => {
              setNewItemModal(false)
              setFormNewItem(newFormDefault);
            }}>X</h3>
          </div>
          <div className="content">
            <div className="left">
              <div className="input">
                <TextInput size={1.3} label='Nome' value={formNewItem.nome} onChange={val => setFormNewItem({...formNewItem, nome: val})}/>
              </div>
              <div className="input">
                <Select size={1.3} caption='Setor' backgroundColor="white" value={formNewItem.setor} options={setores} onChange={val => setFormNewItem({...formNewItem, setor: val})} />
              </div>
              <div className="input">
                <TextInput size={1.3} label='Modelo de Receita' value={formNewItem.modelo} onChange={val => setFormNewItem({...formNewItem, modelo: val})}/>
              </div>
              <div className="input">
                <Select size={1.3} caption='Tecnologia Necessária' backgroundColor="white" value={formNewItem.tecnologia} options={conhecimentos} onChange={val => setFormNewItem({...formNewItem, tecnologia: val})} />
              </div>
              <div className="input">
                <Select size={1.3} caption='Conhecimentos Específicos' backgroundColor="white" value={formNewItem.conhecimento} options={conhecimentos} onChange={val => setFormNewItem({...formNewItem, conhecimento: val})} />
              </div>
            </div>
            <div className="right">
              <div className="input">
                <Select size={1.3} caption='Investimento Inicial' backgroundColor="white" value={formNewItem.investimento} options={conhecimentos} onChange={val => setFormNewItem({...formNewItem, investimento: val})} />  
              </div>
              <div className="textarea">
                <TextInput size={1.3} label='Descrição' value={formNewItem.description} onChange={val => setFormNewItem({...formNewItem, description: val})} multiline={true}/>
              </div>
              <div className="buttons">
                <Button color={Colors.green} caption="Salvar Ideia" onClick={handleSaveNewIdea}/>
              </div>
            </div>
          </div>
        </ModalForm>
      </Modal> 
      <ReactTooltip place="right"/>
      <Top>
        <Tabs>
          <div className={`noselect tab ${tab === 'list' ? 'selected' : ''}`} onClick={() => handleTabClick('list')}><h1>Lista de Ideias</h1></div>
          <div className={`noselect tab ${tab === 'mine' ? 'selected' : ''}`} onClick={() => handleTabClick('mine')}><h1>Minhas Ideias</h1></div>
        </Tabs>
      </Top>
      {tab === 'list' ? 
        <Filters>
          <div className="filter"><Select placeholder="SETOR" onChange={val => setSetor(val)} options={setores}/></div>
          <div className="filter"><Select placeholder="MODELO DE RECEITAS"/></div>
          <div className="filter"><Select placeholder="CONHECIMENTOS ESP." onChange={val => setConhecimento(parseInt(val) || '')} options={conhecimentos}/></div>
          <div className="filter"><Select placeholder="TECH. NECESSÁRIA" onChange={val =>setTecnologia(parseInt(val) || '')} options={tecnologias}/></div>
          <div className="filter"><Select placeholder="CLASSIFICAR POR" onChange={val => setSortBy(val)} options={classificar}/></div>
        </Filters>
      :
        <NewIdea>
          <div className="icon">
            <Idea />
          </div>
          <div className="text">
            <h1>Teve uma idéia de negócios brilhante?</h1>
            <p>Você também pode adicionar ideias à sua lista clicando no botão ao lado.<br />Não se preocupe, elas não serão compartilhadas com ninguém!</p>
          </div>
          <div className="button">
            <Button color={Colors.orange} onClick={() => setNewItemModal(true)} caption="+ Adicionar Ideia" type="round"/>
          </div>
        </NewIdea>
      
      }
      <Table tab={tab}>
        <table>
          <thead>
            <tr>
              <th>Ideia</th>
              <th>Descrição</th>
              <th>Setor</th>
              <th>Modelo de<br />Receitas</th>
              <th>Conhecimentos<br />Específicos</th>
              <th>Tecnologia<br/>Necessária</th>
              <th>Investimento<br />Inicial</th>
              {tab === 'list' &&
                <th></th>
              }
            </tr>
          </thead>
          <tbody>
            {ideas.map((idea, index) => {
              return (
                <tr key={index} id={index} onClick={handleIdeaClick}>
                  <td>{idea.nome || idea.id}</td>
                  <td data-tip={idea.description}>{idea.description}</td>
                  <td>{idea.setor}</td>
                  <td>Transacional</td>
                  <td>{fieldIntensity(idea.conhecimento)}</td>
                  <td>{fieldIntensity(idea.tecnologia)}</td>
                  <td>{fieldInvestimento(idea.investimento)}</td>
                  {tab === 'list' &&
                    <td>
                      <Like onClick={handleLikeClick} data-idea={idea.id} fill={idea.like === 1 ? Colors.green : '#AAA'} />
                      <Like onClick={handleDislikeClick} data-idea={idea.id} fill={idea.like === 0 ? "red" : '#AAA'} className="thumbs-down" />
                      {idea.favorite === null ?
                        <Star onClick={handleFavoriteClick} data-idea={idea.id} fill={Colors.orange} />
                      :
                        <StarFilled onClick={handleFavoriteClick} data-idea={idea.id} fill={Colors.orange} />
                      }
                    </td>
                  }
                </tr>
              )
            })}
          </tbody>
        </table>
      </Table>
    </Container>
  )
}

const mapStateToProps = (state) => ({
  User: state.UserReducer
});
const mapDispatchToProps = (dispatch) => ({
  setMenu: (menu) => dispatch({type: 'CHANGE_MENU', active: menu}),
  showAlert: ({title = '', message = '', callback}) => dispatch({type: 'SHOW_ALERT', title: title, message: message, callback: callback}),
  showConfirm: ({title = '', message = '', onYes, onNo}) => dispatch({type: 'SHOW_CONFIRM', title: title, message: message, onYes: onYes, onNo: onNo}),
  loading: (visible) => dispatch({type: (visible ? 'SHOW_LOADING' : 'HIDE_LOADING')})
});

export default connect(mapStateToProps, mapDispatchToProps)(Ideas);