import React from 'react';
import { useEffect } from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';

import One from './One';
import Two from './Two';
import Three from './Three';
import Four from './Four';
import Five from './Five';
import Six from './Six';
import Seven from './Seven';
import Eight from './Eight';
import Nine from './Nine';
import Ten from './Ten';
import Eleven from './Eleven';
import Twelve from './Twelve';
import Thirteen from './Thirteen';
import Fourteen from './Fourteen';
import Fifteen from './Fifteen';

const StepsContainers = {
  One: One,
  Two: Two,
  Three: Three,
  Four: Four,
  Six: Six,
  Five: Five,
  Seven: Seven,
  Eight: Eight,
  Nine: Nine,
  Ten: Ten,
  Eleven: Eleven,
  Twelve: Twelve,
  Thirteen: Thirteen,
  Fourteen: Fourteen,
  Fifteen: Fifteen
}

const Steps = ({Project}) => {

  useEffect(() => {
    console.log(Project);
  }, [Project])

  switch (Project.step){
    case 1:
      return <StepsContainers.One/>
    case 2:
      return <StepsContainers.Two />
    case 3:
      return <StepsContainers.Three />
    case 4:
      return <StepsContainers.Four />
    case 5:
      return <StepsContainers.Five />
    case 6:
      return <StepsContainers.Six />
    case 7:
      return <StepsContainers.Seven />
    case 8:
      return <StepsContainers.Eight />
    case 9:
      return <StepsContainers.Nine />
    case 10:
      return <StepsContainers.Ten />
    case 11:
      return <StepsContainers.Eleven />
    case 12:
      return <StepsContainers.Twelve />
    case 13:
      return <StepsContainers.Thirteen />
    case 14:
      return <StepsContainers.Fourteen />
    case 15:
      return <StepsContainers.Fifteen />
  }
}

const mapStateToProps = (state) => ({
  Project: state.ProjectReducer
});
const mapDispatchToProps = (dispatch) => ({

});

export default connect(mapStateToProps, mapDispatchToProps)(Steps);