const INITIAL_STATE = { 
  id: 0,
  step: 0
};

const ProjectReducer = (state = INITIAL_STATE, action) => {
switch (action.type){
  case 'SET_PROJECT':
    return {
      ...state,
      description: action.description,
      id: action.id,
      idea_id: action.idea_id,
      nome: action.nome,
      progress: action.progress,
      project_id: action.project_id,
      setor: action.setor,
      start_date: action.start_date,
      step: action.step,
      current_step: action.step,
      step_name: action.step_name
    };
  case 'SET_PROJECT_STEP':
    return {
      ...state,
      step: action.step
    };
  case 'SET_PROJECT_CURRENT_STEP':
    return {
      ...state,
      current_step: action.current_step
    };
  case 'CLEAR_PROJECT':
    return {
      id: 0,
      step: 0
    };
  default:
    return state;
}
}

export default ProjectReducer;