import React from 'react';
import styled from 'styled-components';

import { ReactComponent as Arrow } from './icons/arrow-down.svg';
import { ReactComponent as Bulb } from './icons/bulb.svg';
import { ReactComponent as Trash } from './icons/trash.svg';
import { ReactComponent as News } from './icons/newspaper.svg';

const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: ${props => props.color};
  width: 100%;
  opacity: 1;
  transition: box-shadow 0.3s ease;
  cursor: pointer;
  span {
    font-family: Montserrat-Black;
    font-size: ${props => props.size * 1.6}rem;
    padding: 1.2rem;
  }
    svg {
      height: ${props => props.size * 1.6}rem;
      padding: 0.2rem
    }
  &:active {
    opacity: 0.8;
  }
  &.round {
    border-radius: 50px;
  }
  &.square {
    border-radius: 5px;
  }
`

const Button = ({color = '#0275d8', caption = 'Ok', onClick = () => {}, arrow = false, type = 'square', icon = '', size = 1}) => {

  return (
    <Container color={color} className={`noselect custom-button ${type}`} onClick={onClick} size={size}>
      {arrow ? <Arrow /> : ''}
      {icon === 'bulb' && <Bulb />}
      {icon === 'trash' && <Trash />}
      {icon === 'news' && <News />}
      <span>{caption.toUpperCase()}</span>
    </Container>
  )
}

export default Button;