const INITIAL_STATE = { 
    title: '',
    message: '',
    callback: () => {},
    visible: false
};

const AlertReducer = (state = INITIAL_STATE, action) => {
  switch (action.type){
    case 'SHOW_ALERT':
      return {
        ...state,
        title: action.title,
        message: action.message,
        callback: action.callback,
        visible: true
      };
    case 'HIDE_ALERT':
      return {
        ...state,
        visible: false
      };
    default:
      return state;
  }
}

export default AlertReducer;