const INITIAL_STATE = { 
  title: '',
  message: '',
  onYes: () => {},
  onNo: () => {},
  visible: false
};

const ConfirmReducer = (state = INITIAL_STATE, action) => {
switch (action.type){
  case 'SHOW_CONFIRM':
    return {
      ...state,
      title: action.title,
      message: action.message,
      onYes: action.onYes,
      onNo: action.onNo,
      visible: true
    };
  case 'HIDE_CONFIRM':
    return {
      ...state,
      visible: false
    };
  default:
    return state;
}
}

export default ConfirmReducer;