import React, { useEffect } from 'react';
import { useState } from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import axios from 'axios';

import Colors from '../../../../Components/Colors';
import DotsPages from '../../../../Components/DotsPages';
import Button from '../../../../Components/Button';
import TextInput from '../../../../Components/TextInput';
import Modal from '../../../../Components/Modal';
import Select from '../../../../Components/Select';


const Container = styled.div`
  animation: fadeIn 1s ease;
  .summary {
    display: flex;
    gap: 2rem;
    animation: fadeIn 0.3s ease;
    .left {
      background-color: #EEE;
      flex: 1;
      display: flex;
      flex-direction: column;
      padding: 2rem;
      border-radius: 5px;
      h1 {
        font-family: Montserrat-Bold;
        font-size: 3rem;
      }
      div {
        display: flex;
        align-items: center;

        .caption {
          font-family: Montserrat-Bold;
          font-size: 7rem;
          min-width: 10rem;
        }
        .text {
          font-family: Montserrat-Medium;
          font-size: 1.8rem;
        }
      }
    }
    .right {
      background-color: #EEE;
      flex: 0.6;
      padding: 2rem;
      border-radius: 5px;
      h1 {
        font-family: Montserrat-Bold;
        font-size: 3rem;
      }
      p {
        font-family: Montserrat-Medium;
        font-size: 1.8rem;
        line-height: 1.5;
      }
    }
  }
  .form {
    display: flex;
    gap: 2rem;
    animation: fadeIn 0.3s ease;
    .left {
      flex: 1;
      padding: 2rem;  
      background-color: #EEE;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      .formItem {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 1.6rem;
        p {
          font-size: 1.6rem;
          font-family: Montserrat-Bold;
          min-width: 30rem;
        }
        .input {
          min-width: 40rem;
          margin-left: 3rem;
        }
        .help {
          border: 3px solid black;
          font-family: Montserrat-Bold;
          width: 3rem;
          height: 3rem;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 50%;
          font-size: 2rem;
          margin-left: 2rem;
          cursor: pointer;
        }
      }
    }
    .right {
      flex: 0.4;
      .materials {
        border: 2px solid #BBB;
        background-color: #EEE;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        padding: 2.5rem;
        h1 {
          font-size: 2.6rem;
          text-align: center;
        }
        div {
          display: flex;
          align-items: center;
          cursor: pointer;
          padding: 1rem;
          img {
            width: 4rem;
            height: 4rem;
          }
          p {
            font-size: 1.6rem;
            font-family: Montserrat-Medium;
            margin-left: 2rem;
          }
          &:hover {
            background-color: #BBB;
          }
        }
      }
      @media screen and (max-width: 1366px){
        flex: 0.5;
      }
    }
  }
  .end {
    display: flex;
    gap: 2rem;
    animation: fadeIn 0.3s ease;
    .left {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      .diagnosys,
      .disclaimer {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        h1 {
          margin: 0;
          font-size: 2.4rem;
          font-family: Montserrat-Bold;
        }
        p {
          font-size: 1.6rem;
          background-color: #F8F8F8;
          padding: 2rem;
          font-family: Montserrat-Medium;
          line-height: 1.3;
          span {
            color: #7ED957;
            font-size: 2.4rem;
            font-family: Montserrat-Bold;
          }
        }
      }
    }
    .right {
      flex: 0.25;
      display: flex;
      flex-direction: column;
      align-items: center;
      .grade,
      .benchmark {
        display: flex;
        flex-direction: column;
        align-items: center;
        h1 {
          margin: 0;
          font-family: Montserrat-Bold;
          font-size: 2.4rem;
        }
        p {
          text-align: center;
          font-size: 1.6rem;
          background-color: #F8F8F8;
          padding: 2rem;
          font-family: Montserrat-Medium;
          line-height: 1.3;
        }
        h2 {
          text-align: center;
          font-size: 3rem;
          background-color: #F8F8F8;
          padding: 2rem;
          font-family: Montserrat-Bold;
          color: #7ED957;
          min-width: 30rem;
        }
      }
    }
  }
`

const Buttons = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  .no-button {
    min-width: 20rem;
  }
  .custom-button {
    max-width: 20rem;
  }
`

const Summary = () => {
  return (
    <div className="summary">
      <div className="left">
        <h1>Como Usar?</h1>
        <div className="first">
          <div className="caption">1º</div>
          <div className="text">Leia os relatórios da BusinessPal sobre o seu setor para conseguir informações sobre o setor em que você pretende se inserir</div>
        </div>
        <div className="second">
          <div className="caption">2º</div>
          <div className="text">Complemente sua pesquisa fazendo buscas na internet e coletando informações sobre o setor.</div>
        </div>
        <div className="third">
          <div className="caption">3º</div>
          <div className="text">Nossa ferramenta fará 5 perguntas simples para te ajudar a definir o tipo do mercado no qual você quer se inserir. Você receberá um diagnóstico personalizado na mesma hora!</div>
        </div>
      </div>
      <div className="right">
        <h1>Por que é importante?</h1>
        <p>O tipo de mercado é uma variável que influenciará a estratégia que deve ser utilizada pelos empreendedores.</p>
        <p>Ter essa informação em mãos permite planejar melhor os recursos necessários para iniciar o projeto e, também, construir modelos de previsão mais precisos em relação à necessidade de capital que será necessária para fazê-lo crescer.</p>
      </div>

    </div>
  )
}

const Form = ({form, setForm, Project}) => {

  const options = [
    {value: 'Sim', caption: 'Sim'},
    {value: 'Não', caption: 'Não'}
  ]

  useEffect(() => {
      axios.post(`${process.env.REACT_APP_API_URL}api/steps/getData`, {step: Project.step, project_id: Project.project_id})
        .then(response => {
          setForm({...JSON.parse(response.data)});
        })
  }, [])

  return (
    <div className="form">
      <div className="left">
        <div className="formItem">
          <p>Atualmente, há um mercado definido, estabilizado e com grande volume de clientes e concorrentes para o seu produto?</p>
          <div className="input">
            <Select options={options} size={1.3} value={form.Q1} onChange={val => setForm({...form, Q1: val})} />
          </div>
        </div>
        <div className="formItem">  
          <p>Existem, nesse mercado, diversos grupos de clientes com prioridades diferentes em relação ao seu produto?</p>
          <div className="input">
          <Select options={options} size={1.3} value={form.Q2} onChange={val => setForm({...form, Q2: val})} />
          </div>
        </div>
        <div className="formItem">
          <p>Já existe, em algum outro lugar do mundo, algum público familiarizado com este produto?</p>
          <div className="input">
          <Select options={options} size={1.3} value={form.Q3} onChange={val => setForm({...form, Q3: val})} />
          </div>
        </div>
      </div>
    </div>
  )
}

const End = () => {
  return (
    <div className="end">
      <div className="left">
        <div className="diagnosys">
          <h1>Nosso Diagnóstico</h1>
          <p><span>Ressignificação de Mercado</span><br /><br />A estratégia relacionada à ressignificação de mercado leva em conta a diversificação de clientes dentro do seu segmento. Para criar um produto de sucesso, foque inicialmente em um só nicho que tem a dor que você resolve muito presente. Dessa forma, você conseguirá conquista-lo e ganhar autoridade para expandir para outros nichos.</p>
        </div>
        <div className="disclaimer">
          <h1>Exemplo Prático</h1>
          <p>O Facebook escolheu uma faculdade para o lançamento da sua plataforma, quando o produto já era um sucesso, as pessoas começaram a pedir pela expansão. Dessa forma, o Facebook adicionou mais universidades ao portfólio e, quando alcançou mais prestígio, abriu a rede social para todas as pessoas.</p>
        </div>
      </div>
      <div className="right">
        <div className="benchmark">
          <h1>Disclaimer</h1>
          <p>O objetivo desta ferramenta não é determinar se sua ideia é boa ou ruim, mas dar uma perspectiva, de acordo com um consenso do mercado, das suas possibilidades de sucesso. Por isso, os resultados não devem ser interpretados como uma resposta definitiva, mas como uma nova variável que te ajudará a decidir se deverá construir ou não uma empresa como esta.</p>
        </div>
      </div>
    </div>
  )
}

const Two = ({Project, showAlert, setProjectStep, setProjectCurrentStep}) => {

  const [page, setPage] = useState(1);
  const [form, setForm] = useState({
    Q1: '',
    Q2: '',
    Q3: ''
  })

  const handleNextButton = () => {
    if(Project.current_step !== 2) return setPage(page+1);
    let error = Object.values(form).some(item => item === '');
    if(error) {
      showAlert({
        title: 'Erro',
        message: 'Todos os campos são obrigatórios'
      })
    }
    else {
      axios.post(`${process.env.REACT_APP_API_URL}api/steps/save`, {step: Project.current_step, project_id: Project.project_id, data: JSON.stringify(form)})
        .then(response => {
          if(response.status === 200){
            setPage(page + 1);
          }
        })
    }
  }

  const handleFinishButton = () => {
    axios.post(`${process.env.REACT_APP_API_URL}api/projects/update/${Project.project_id}`, {data: {step: (Project.current_step + 1)}})
      .then(response => {
        if(response.status === 200) {
          setProjectCurrentStep(Project.current_step + 1);
          setProjectStep({step: (Project.step + 1)});
        }
      })
  }

  return (
    <Container colors={Colors}>
      {page === 1 && <Summary />}
      {page === 2 && <Form form={form} setForm={setForm} Project={Project}/>}
      {page === 3 && <End />}
      <Buttons>
        {page === 1 ?
          <div className="no-button"></div>  
        :
          <Button caption="Voltar" color={Colors.orange} onClick={() => setPage(page - 1)}/>  
        }
        <DotsPages page={page} pages={3} setPage={setPage}/>
        {page === 3 && Project.current_step === 2 && <Button caption="Concluir" color={Colors.green} onClick={handleFinishButton}/>}
        {page === 2 && <Button caption="Avançar" color={Colors.green} onClick={handleNextButton}/>}
        {page === 1 && <Button caption="Avançar" color={Colors.green} onClick={() => setPage(page + 1)}/>}
      </Buttons>
    </Container>
  )
}

const mapStateToProps = (state) => ({
  Project: state.ProjectReducer
});
const mapDispatchToProps = (dispatch) => ({
  showAlert: (props = {title: '', message: '', callback: undefined}) => dispatch({type: 'SHOW_ALERT', ...props}),
  setProjectStep: ({step = 0}) => dispatch({type: 'SET_PROJECT_STEP', step: step}),
  setProjectCurrentStep: (step = 0) => dispatch({type: 'SET_PROJECT_CURRENT_STEP', current_step: step})
});

export default connect(mapStateToProps, mapDispatchToProps)(Two);