import React, { useEffect } from 'react';
import { useState } from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import axios from 'axios';

import Colors from '../../../../Components/Colors';
import DotsPages from '../../../../Components/DotsPages';
import Button from '../../../../Components/Button';
import TextInput from '../../../../Components/TextInput';
import Modal from '../../../../Components/Modal';
import Select from '../../../../Components/Select';


const Container = styled.div`
  animation: fadeIn 1s ease;
  .summary {
    display: flex;
    gap: 2rem;
    animation: fadeIn 0.3s ease;
    .left {
      background-color: #EEE;
      flex: 1;
      display: flex;
      flex-direction: column;
      padding: 2rem;
      border-radius: 5px;
      h1 {
        font-family: Montserrat-Bold;
        font-size: 3rem;
      }
      div {
        display: flex;
        align-items: center;

        .caption {
          font-family: Montserrat-Bold;
          font-size: 7rem;
          min-width: 10rem;
        }
        .text {
          font-family: Montserrat-Medium;
          font-size: 1.8rem;
        }
      }
    }
    .right {
      background-color: #EEE;
      flex: 0.6;
      padding: 2rem;
      border-radius: 5px;
      h1 {
        font-family: Montserrat-Bold;
        font-size: 3rem;
      }
      p {
        font-family: Montserrat-Medium;
        font-size: 1.8rem;
        line-height: 1.5;
      }
    }
  }
  .form {
    display: flex;
    gap: 2rem;
    animation: fadeIn 0.3s ease;
    .left {
      flex: 1;
      padding: 2rem;  
      background-color: #EEE;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      .formItem {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 1.6rem;
        p {
          font-size: 2rem;
          font-family: Montserrat-Bold;
          min-width: 30rem;
          .subtext {
            font-size: 1.4rem;
            color: rgba(0,0,0,0.7);
          }
        }
        .input {
          min-width: 50rem;
          margin-left: 3rem;
        }
        .help {
          border: 3px solid black;
          font-family: Montserrat-Bold;
          width: 3rem;
          height: 3rem;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 50%;
          font-size: 2rem;
          margin-left: 2rem;
          cursor: pointer;
        }
      }
    }
    .right {
      flex: 0.4;
      .materials {
        border: 2px solid #BBB;
        background-color: #EEE;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        padding: 2.5rem;
        h1 {
          font-size: 2.6rem;
          text-align: center;
        }
        div {
          display: flex;
          align-items: center;
          cursor: pointer;
          padding: 1rem;
          img {
            width: 4rem;
            height: 4rem;
          }
          p {
            font-size: 1.6rem;
            font-family: Montserrat-Medium;
            margin-left: 2rem;
          }
          &:hover {
            background-color: #BBB;
          }
        }
      }
      @media screen and (max-width: 1366px){
        flex: 0.5;
      }
    }
  }
  .end {
    display: flex;
    gap: 2rem;
    animation: fadeIn 0.3s ease;
    .left {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      .diagnosys,
      .disclaimer {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        h1 {
          margin: 0;
          font-size: 2.4rem;
          font-family: Montserrat-Bold;
        }
        p {
          font-size: 1.6rem;
          background-color: #F8F8F8;
          padding: 2rem;
          font-family: Montserrat-Medium;
          line-height: 1.3;
          span {
            color: #7ED957;
            font-size: 2.4rem;
            font-family: Montserrat-Bold;
          }
        }
      }
    }
    .right {
      flex: 0.25;
      display: flex;
      flex-direction: column;
      align-items: center;
      .grade,
      .benchmark {
        display: flex;
        flex-direction: column;
        align-items: center;
        h1 {
          margin: 0;
          font-family: Montserrat-Bold;
          font-size: 2.4rem;
        }
        p {
          text-align: center;
          font-size: 1.6rem;
          background-color: #F8F8F8;
          padding: 2rem;
          font-family: Montserrat-Medium;
          line-height: 1.3;
        }
        h2 {
          text-align: center;
          font-size: 3rem;
          background-color: #F8F8F8;
          padding: 2rem;
          font-family: Montserrat-Bold;
          color: #7ED957;
          min-width: 30rem;
        }
      }
    }
  }
`

const Buttons = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  .no-button {
    min-width: 20rem;
  }
  .custom-button {
    max-width: 20rem;
  }
`

const Summary = () => {
  return (
    <div className="summary">
      <div className="left">
        <h1>Como Usar?</h1>
        <div className="first">
          <div className="caption">1º</div>
          <div className="text">Nesta etapa, o objetivo é analisar cada uma das atividades que você inseriu em alguns passos anteriores e traçar estratégias para atingir o cliente em cada momento.</div>
        </div>
        <div className="second">
          <div className="caption">2º</div>
          <div className="text">Escolha a estratégia pela nossa lista. Nossa ferramenta compilará os canais mais citados, que, naturalmente, são opções melhores para o início da empresa.</div>
        </div>
        <div className="second">
          <div className="caption">3º</div>
          <div className="text">Se quiser, você pode adicionar mais atividades pertinentes na análise, mesmo que não tenha as inserido em passos anteriores.</div>
        </div>
      </div>
      <div className="right">
        <h1>Por que é importante?</h1>
        <p>O Guia de Aquisição de Clientes será a espinha dorsal da estratégia de aquisição de clientes para a sua empresa.</p>
        <p>Por meio dele, você poderá analisar todas as atividades que seu cliente-alvo pratica e decidir qual é a melhor forma de interagir com ele em cada momento. Dessa forma, você descobrirá quais canais tendem a ser mais eficientes nessa comunicação.</p>
      </div>

    </div>
  )
}

const Form = ({form, setForm, Project}) => {

  const options = [
    {value: 'Sim', caption: 'Sim'},
    {value: 'Não', caption: 'Não'}
  ]

  const mercado_options = [
    {value: 'Monopólio', caption: 'Monopólio'},
    {value: 'Duopólio', caption: 'Duopólio'},
    {value: 'Líder de Mercado', caption: 'Líder de Mercado'},
    {value: 'Mercado Volátil', caption: 'Mercado Volátil'},
    {value: 'Mercado Aberto', caption: 'Mercado Aberto'}
  ]

  const perspectiva_options = [
    {value: 'Muito Forte (+10% ao ano)', caption: 'Muito Forte (+10% ao ano)'},
    {value: 'Forte (Entre 5% e 10% ao ano)', caption: 'Forte (Entre 5% e 10% ao ano)'},
    {value: 'Estagnado (Entre 0% e 5% ao ano)', caption: 'Estagnado (Entre 0% e 5% ao ano)'},
    {value: 'Fraco (Entre -5% e 0% ao ano)', caption: 'Fraco (Entre -5% e 0% ao ano)'},
    {value: 'Muito Fraco (Entre -10% e -5% ao ano)', caption: 'Muito Fraco (Entre -10% e -5% ao ano)'}
  ]

  const indicios_options = [
    {value: 'Tenho fortes indícios', caption: 'Tenho fortes indícios'},
    {value: 'Tenho alguns indícios', caption: 'Tenho alguns indícios'},
    {value: 'Tenho poucos indícios', caption: 'Tenho poucos indícios'},
    {value: 'Não tenho nenhum indício', caption: 'Não tenho nenhum indício'}
  ]

  const certeza_options = [
    {value: 'Tenho 100% de certeza', caption: 'Tenho 100% de certeza'},
    {value: 'Tenho alguma certeza', caption: 'Tenho alguma certeza'},
    {value: 'Tenho pouca certeza', caption: 'Tenho pouca certeza'},
    {value: 'Não tenho nenhuma certeza', caption: 'Não tenho nenhuma certeza'}
  ]
  
  useEffect(() => {
      axios.post(`${process.env.REACT_APP_API_URL}api/steps/getData`, {step: Project.step, project_id: Project.project_id})
        .then(response => {
          setForm({...JSON.parse(response.data)});
        })
  }, [])

  return (
    <div className="form">
      <div className="left">
        <div className="formItem">
          <p>Dê uma nota de 0 a 10 para o tamanho do seu mercado<br /><span className="subtext">Recomendamos que dê a nota que recebeu no Passo 1, quando avaliou o tamanho do seu mercado</span></p>
          <div className="input">
            <TextInput type="number" max="10" size={1.8} value={form.Q1} onChange={val => setForm({...form, Q1: val})} />
          </div>
        </div>
        <div className="formItem">  
          <p>Quão concentrado é o mercado em que você pretende se inserir?<br /><span className="subtext">Recomendamos que siga o diagnóstico que recebeu no Passo 3, quando avaliou a concentração do seu mercado.</span></p>
          <div className="input">
            <Select options={mercado_options} size={1.3} value={form.Q2} onChange={val => setForm({...form, Q2: val})} />
          </div>
        </div>
        <div className="formItem">
          <p>Qual é a perspectiva de crescimento para os próximos anos neste mercado?<br /><span className="subtext">Recomendamos que leia os relatórios da BusinessPal para avaliar a perspectiva de crescimento futuro no seu segmento.</span></p>
          <div className="input">
            <Select options={perspectiva_options} size={1.3} value={form.Q3} onChange={val => setForm({...form, Q3: val})} />
          </div>
        </div>
        <div className="formItem">
          <p>Quanta certeza você tem de que esse problema/desejo é real e latente?</p>
          <div className="input">
            <Select options={indicios_options} size={1.3} value={form.Q5} onChange={val => setForm({...form, Q5: val})} />
          </div>
        </div>
        <div className="formItem">
          <p>Quanta certeza você tem de que consegue construir ou mobilizar um bom time para construir uma solução para o problema/desejo que pretende resolver?</p>
          <div className="input">
            <Select options={certeza_options} size={1.3} value={form.Q4} onChange={val => setForm({...form, Q4: val})} />
          </div>
        </div>
      </div>
    </div>
  )
}

const End = () => {
  return (
    <div className="end">
      <div className="left">
        <div className="diagnosys">
          <h1>Nosso Diagnóstico</h1>
          <p><span>Mercado Volátil</span><br /><br />Nessa circunstância, a maior empresa do mercado ainda não é capaz de influenciá-lo e, por isso, pode ser que existam tanto oportunidades em mercados existentes como em novos nichos. Dessa forma, você deve analisar objetivamente a capacidade das empresas líderes do mercado de entregar o que é prometido. Caso elas façam um bom trabalho, isso é um indicativo de que você deve buscar um nicho menor nesse mercado. Caso haja insatisfação de clientes, é um bom sinal de que aquele mercado existente necessita de novas empresas.</p>
        </div>
        <div className="disclaimer">
          <h1>Exemplo Prático</h1>
          <p>A dr.Consulta, startup brasileira que oferece consultas, exames e procedimentos à baixo custo, percebeu que, no mercado de saúde brasileiro, havia um espaço muito grande entre as pessoas que podiam pagar por um plano de saúde e as que tinham que utilizar o SUS. Dessa forma, a empresa criou um modelo que a permite cobrar um preço acessível pelo atendimento médico focado nas pessoas que estavam no meio das duas faixas de renda. Assim, apesar de concorrer com empresas grandes, a startup conseguiu prosperar diferenciando seu produto.</p>
        </div>
      </div>
      <div className="right">
        <div className="benchmark">
          <h1>Disclaimer</h1>
          <p>O objetivo desta ferramenta não é determinar se sua ideia é boa ou ruim, mas dar uma perspectiva, de acordo com um consenso do mercado, das suas possibilidades de sucesso. Por isso, os resultados não devem ser interpretados como uma resposta definitiva, mas como uma nova variável que te ajudará a decidir se deverá construir ou não uma empresa como esta.</p>
        </div>
      </div>
    </div>
  )
}

const Four = ({Project, showAlert, setProjectStep, setProjectCurrentStep}) => {

  const [page, setPage] = useState(1);
  const [form, setForm] = useState({
    Q1: '',
    Q2: '',
    Q3: '',
    Q4: '',
    Q5: ''
  })

  const handleNextButton = () => {
    if(Project.current_step !== 4) return setPage(page+1);
    let error = Object.values(form).some(item => item === '');
    if (form.Q1 > 10 || form.Q1 < 0) return showAlert({title: 'Erro', message: 'A nota deve ser entre 0 e 10'});
    if(error) {
      showAlert({
        title: 'Erro',
        message: 'Todos os campos são obrigatórios'
      })
    }
    else {
      axios.post(`${process.env.REACT_APP_API_URL}api/steps/save`, {step: Project.current_step, project_id: Project.project_id, data: JSON.stringify(form)})
        .then(response => {
          if(response.status === 200){
            setPage(page + 1);
          }
        })
    }
  }

  const handleFinishButton = () => {
    axios.post(`${process.env.REACT_APP_API_URL}api/projects/update/${Project.project_id}`, {data: {step: (Project.current_step + 1)}})
      .then(response => {
        if(response.status === 200) {
          setProjectCurrentStep(Project.current_step + 1);
          setProjectStep({step: (Project.step + 1)});
        }
      })
  }

  return (
    <Container colors={Colors}>
      {page === 1 && <Summary />}
      {page === 2 && <Form form={form} setForm={setForm} Project={Project}/>}
      {page === 3 && <End />}
      <Buttons>
        {page === 1 ?
          <div className="no-button"></div>  
        :
          <Button caption="Voltar" color={Colors.orange} onClick={() => setPage(page - 1)}/>  
        }
        <DotsPages page={page} pages={3} setPage={setPage}/>
        {page === 3 && Project.current_step === 4 && <Button caption="Concluir" color={Colors.green} onClick={handleFinishButton}/>}
        {page === 2 && <Button caption="Avançar" color={Colors.green} onClick={handleNextButton}/>}
        {page === 1 && <Button caption="Avançar" color={Colors.green} onClick={() => setPage(page + 1)}/>}
      </Buttons>
    </Container>
  )
}

const mapStateToProps = (state) => ({
  Project: state.ProjectReducer
});
const mapDispatchToProps = (dispatch) => ({
  showAlert: (props = {title: '', message: '', callback: undefined}) => dispatch({type: 'SHOW_ALERT', ...props}),
  setProjectStep: ({step = 0}) => dispatch({type: 'SET_PROJECT_STEP', step: step}),
  setProjectCurrentStep: (step = 0) => dispatch({type: 'SET_PROJECT_CURRENT_STEP', current_step: step})
});

export default connect(mapStateToProps, mapDispatchToProps)(Four);