import React from 'react';
import styled from 'styled-components';
import Button from './Button';

import { connect } from 'react-redux';
import Colors from './Colors';

const AlertScreen = ({visible = false, message = '', title = '', callback, dispatch, Alert}) => {
  
  const Container = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.7);
    display: ${props => (props.visible ? 'flex' : 'none')};
    align-items: center;
    justify-content: center;
    z-index: 100;
  `

  const Card = styled.div`
    max-width: 50%;
    max-height: 40%;
    width: fit-content;
    height: fit-content;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 3rem;
    border-radius: 5px;
  `

  const Title = styled.h1`
    font-size: 2.6rem;
  `

  const Message = styled.p`
    font-size: 2rem;
    pre {
      font-family: Montserrat-MediumItalic;
    }
  `
  const ButtonContainer = styled.div`
    width: 300px;
    margin: 2rem;
  `

  const handleOkClick = () => {
    if(typeof callback === 'function') callback();
    dispatch({
      type: 'HIDE_ALERT'
    });
  }

  return (
    <Container visible={visible}>
      <Card>
        <Title>{title}</Title>
        <Message><pre>{message}</pre></Message>
        <ButtonContainer>
          <Button onClick={handleOkClick} color={Colors.green}>Ok</Button>
        </ButtonContainer>
      </Card>
    </Container>
  )

}

export default connect(state => ({Alert: state.AlertReducer}))(AlertScreen);