import React from 'react';
import styled from 'styled-components';
import Button from './Button';

import { connect } from 'react-redux';
import Colors from './Colors';

const Container = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0,0,0,0.7);
  display: ${props => (props.visible ? 'flex' : 'none')};
  align-items: center;
  justify-content: center;
  z-index: 100;
`

const Card = styled.div`
  max-width: 40%;
  max-height: 40%;
  width: fit-content;
  height: fit-content;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3rem;
  border-radius: 5px;
`

const Title = styled.h1`
  font-size: 2.6rem;
`

const Message = styled.p`
  font-size: 2rem;
  text-align: center;
`
const ButtonContainer = styled.div`
  display: flex;
  width: 400px;
  margin: 2rem;
  .custom-button {
    margin: 1rem;
  }
`


const AlertScreen = ({visible = false, message = '', title = '', onYes = () => {}, onNo = () => {}, dispatch, Alert}) => {
  

  const handleYesClick = () => {
    if(typeof onYes === 'function') onYes();
    dispatch({
      type: 'HIDE_CONFIRM'
    });
  }
  const handleNoClick = () => {
    if(typeof onNo == 'function') onNo();
    dispatch({
      type: 'HIDE_CONFIRM'
    });
  }

  return (
    <Container visible={visible}>
      <Card>
        <Title>{title}</Title>
        <Message>{message}</Message>
        <ButtonContainer>
          <Button color={Colors.green} onClick={handleYesClick} caption="sim" />
          <Button color={Colors.orange} onClick={handleNoClick} caption="Não" />
        </ButtonContainer>
      </Card>
    </Container>
  )

}

export default connect(state => ({Alert: state.AlertReducer}))(AlertScreen);