import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';

import Button from '../../Components/Button';
import Colors from '../../Components/Colors';
import Modal from '../../Components/Modal';
import Select from '../../Components/Select';
import TextInput from '../../Components/TextInput';

import { ReactComponent as Like } from '../../Components/icons/like.svg';
import { ReactComponent as Download } from '../../Components/icons/download.svg';
import { useState } from 'react';
import axios from 'axios';

const Container = styled.div`
  width: auto; 
  margin: 5rem 10rem;
  a{
    text-decoration: none;
  }
  overflow: hidden;
`

const Top = styled.div`
  display: flex;
  align-items: center;
  h1 {
    flex: 0;
    font-size: 6rem;
    margin: 0rem;
  }
  p{
    flex: 1;
    font-size: 2rem;
    text-align: right;
    font-family: Montserrat-Medium;
    margin: 0rem 3rem 0rem 0rem;
  }
  .button {
    flex: 0;
    height: 3rem;
    min-width: 30rem;
    display: flex;
    align-items: center;
  }
`

const Content = styled.div`
  border-top: 3px solid black;
  ul {
    padding: 0px;
    li {
      border-radius: 10px;
      &:nth-child(odd) {
        background-color: #DDD;
      }
      list-style-type: none;
      display: flex;
      align-items: center;
      min-height: 12rem;
      .title {
        margin-left: 2rem;;
        flex: 1;
        h2 {
          font-size: 2.6rem;
        }
        p {
          font-size: 1.8rem;
          font-family: Montserrat-MediumItalic;
        }
      }
      svg {
        height: 4rem;
        padding: 2rem;
        &.down {
          transform: rotate(180deg)
        }
      }
    }
  }
`

const RequestReport = styled.div`
  .top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 2rem;
    h1 {
      font-size: 3rem;
      margin: 0;
    }
    p {
      font-size: 1.6rem;
      font-family: Montserrat-Bold;
      cursor: pointer;
      padding-right: 1rem;
      margin: 0;
    }
  }
  .form {
    display: flex;
    .left {
      flex: 0.8
    }
    .right{
      flex: 1;
      display: flex;
      flex-direction: column;
      .formItem {
        flex: 1;
      }
      .buttons {
        flex: 1;
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
        .custom-button {
          max-width: 24rem;
        }
      }
    }
    .formItem {
      margin: 1rem;
    }
  }
`
const RequestReportInstructions = styled.div`
  width: 100%;
  *{
    margin: 0;
    padding: 0;
  }
  .top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 2rem;
    h1 {
      font-size: 3rem;
    }
    p {
      font-size: 1.6rem;
      font-family: Montserrat-Bold;
      cursor: pointer;
      padding-right: 1rem;;
    }
  }
  .content {
    font-family: Montserrat-Medium;
    font-size: 1.8rem;
    p {
      margin: 1rem 0rem;
    }
    li {
      margin: 2rem;
    }
  }
  .buttons {
    display: flex;
    justify-content: flex-end;
    .custom-button {
      max-width: 26rem;
    }
  }
`

const Reports = ({loading, showAlert}) => {

  const setores = [
    {value: 'Financeiro', caption: 'Financeiro'},
    {value: 'Saúde e Fitness', caption: 'Saúde e Fitness'},
    {value: 'Educação', caption: 'Educação'},
    {value: 'Varejo', caption: 'Varejo'},
    {value: 'Recursos Humanos', caption: 'Recursos Humanos'},
    {value: 'Serviços Empresariais', caption: 'Serviços Empresariais'},
    {value: 'Marketing', caption: 'Marketing'},
    {value: 'Mobilidade e Transportes', caption: 'Mobilidade e Transportes'},
    {value: 'Alimentação', caption: 'Alimentação'},
    {value: 'Logística', caption: 'Logística'},
    {value: 'Construção Civil', caption: 'Construção Civil'},
    {value: 'Esportes', caption: 'Esportes'},
    {value: 'Agricultura e Pecuária', caption: 'Agricultura e Pecuária'},
    {value: 'Eventos', caption: 'Eventos'},
    {value: 'TI', caption: 'TI'},
    {value: 'Governo', caption: 'Governo'},
    {value: 'Serviços Residenciais', caption: 'Serviços Residenciais'},
    {value: 'Turismo', caption: 'Turismo'},
    {value: 'PET', caption: 'PET'},
    {value: 'Jurídico', caption: 'Jurídico'},
    {value: 'Beleza e Cosméticos', caption: 'Beleza e Cosméticos'},
    {value: 'Energia e Manejo de Resíduos', caption: 'Energia e Manejo de Resíduos'},
    {value: 'Música e Artes', caption: 'Música e Artes'},
    {value: 'Relacionamentos', caption: 'Relacionamentos'},
    {value: 'Imobiliário', caption: 'Imobiliário'}
  ];

  const [modal, setModal] = useState({
    requestReport: false,
    requestReportInstructions: false
  })

  const [reportRequestForm, setReportRequestForm] = useState({
    setor: '',
    name: '',
    email: '',
    description: ''
  })

  const handleSendRequest = () => {
    let validate = Object.values(reportRequestForm).some(val => val === '')
    if(validate) return showAlert({title: 'Erro no preenchimento', message: 'Todos os campos são obrigatórios, por favor verifique os dados digitados.'});
    loading(true);
    axios.post(`${process.env.REACT_APP_API_URL}api/reports/newRequest`, reportRequestForm)
      .then(response => {
        if(response.status === 200){
          loading(false);
          showAlert({
            title: 'Solicitação Enviada',
            message: "Sua Solicitação foi enviada com sucesso!\n\nEm até 24h você receberá o resultado da análise\nsobre seu relatório!",
            callback: () => {
              setModal({...modal, requestReport: false})
              setReportRequestForm({
                setor: '',
                name: '',
                email: '',
                description: ''
              })
            }
          })
        }
      })
  }

  return (
    <Container>
      <Modal visible={modal.requestReport}>
        <RequestReport>
          <div className="top">
            <h1>Solicitar Relatório</h1>
            <p className="close" onClick={() => {
              setModal({...modal, requestReport: false})
              setReportRequestForm({
                setor: '',
                name: '',
                email: '',
                description: ''
              })
            }}>X</p>
          </div>
          <div className="form">
            <div className="left">
              <div className="formItem">
                <Select  size={1.3} caption="Setor" options={setores} backgroundColor="white" onChange={val => setReportRequestForm({...reportRequestForm, setor: val})}/>
              </div>
              <div className="formItem">
                <TextInput size={1.3} label="Seu Nome Completo" value={reportRequestForm.name} onChange={val => setReportRequestForm({...reportRequestForm, name: val})}/>
              </div>
              <div className="formItem">
                <TextInput size={1.3} label="Seu e-mail" value={reportRequestForm.email} onChange={val => setReportRequestForm({...reportRequestForm, email: val})}/>
              </div>
            </div>
            <div className="right">
              <div className="formItem">
                <TextInput size={1.3} label="Descreva o que gostaria de ver no relatório" value={reportRequestForm.description} onChange={val => setReportRequestForm({...reportRequestForm, description: val})} multiline={true}/>
              </div>
              <div className="buttons">
                <Button color={Colors.green} caption="Solicitar Relatório" onClick={handleSendRequest}/>
              </div>
            </div>
          </div>
        </RequestReport>
      </Modal>
      <Modal visible={modal.requestReportInstructions}>
        <RequestReportInstructions>
          <div className="top">
            <h1>Informações para solicitar um relatório</h1>
            <p className="close" onClick={() => setModal({...modal, requestReportInstructions: false})}>X</p>
          </div>
          <div className="content">
            <p>Para solicitar um relatório, você deve declarar que está ciente sobre as informações abaixo:</p>
            <ul>
              <li>
                <p><b>Todos</b> os relatórios estão inclusos na assinatura. Dessa forma, não há necessidade ou obrigação de fazer pagamentos extra para recebê-los;</p>
              </li>
              <li>
                <p>A BusinessPal reservará o direito de somente emitir relatório sobre <b>macro setores da economia</b>. Dessa forma, sua solicitação passará por uma <b>análise de viabilidade</b> e, caso esteja fora do escopo, pode ser <b>rejeitada</b>. Para conversar sobre <b>micro setores</b> utilize o <b>suporte</b></p>
              </li>
              <li>
                <p>O <b>prazo médio de entrega</b> dos relatórios é de <b>15 dias úteis</b> após a confirmação da solicitação</p>
              </li>
              <li>
                <p>A utilização dos relatórios é <b>exclusiva</b> para assinantes da plataforma e qualquer compartilhamento sem autorização será entendido como uma violação de propriedade e tratado como tal</p>
              </li>
            </ul>
          </div>
          <div className="buttons">
            <Button color={Colors.green} caption="Continuar" onClick={() => setModal({...modal, requestReportInstructions: false, requestReport: true})}/>
          </div>
        </RequestReportInstructions>
      </Modal>
      <Top>
        <h1>Relatórios</h1>
        <p>Sentiu falta de algum relatório?</p>
        <div className="button">
          <Button caption="Solicitar Relatório" type="round" color={Colors.orange} icon="news" onClick={() => setModal({...modal, requestReportInstructions: true})}/>
        </div>
      </Top>
      <Content>
        <ul>
          <li>
            <div className="title">
              <h2>Relatório BusinessPal sobre o Setor de Educação</h2>
              <p>1º Semestre 2021</p>
            </div>
            <Download />
            <Like fill="#BBB" />
            <Like className="down" fill="#BBB"/>
          </li>
          <li>
            <div className="title">
              <h2>Relatório BusinessPal sobre o Setor de Educação</h2>
              <p>1º Semestre 2021</p>
            </div>
            <Download />
            <Like fill="#BBB" />
            <Like className="down" fill="#BBB"/>
          </li>
          <li>
            <div className="title">
              <h2>Relatório BusinessPal sobre o Setor de Educação</h2>
              <p>1º Semestre 2021</p>
            </div>
            <Download />
            <Like fill="#BBB" />
            <Like className="down" fill="#BBB"/>
          </li>
        </ul>
      </Content>
    </Container>
  )
}

const mapStateToProps = (state) => ({

});
const mapDispatchToProps = (dispatch) => ({
  loading: status => dispatch({type: (status ? 'SHOW_LOADING' : 'HIDE_LOADING')}),
  showAlert: ({title = '', message = '', callback}) => dispatch({type: 'SHOW_ALERT', title: title, message: message, callback: callback})
});

export default connect(mapStateToProps, mapDispatchToProps)(Reports);