import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  h1 {
    margin: 0;
    font-size: 8rem;
    font-family: 'Courier New', Courier, monospace;
    color: #DDD;
    span {
      cursor: pointer;
      &.active {
        color: black;
      }
    }
  }
`

const DotsPages = ({pages, page, setPage}) => {

  const Dots = () => {
    switch (pages){
      case 2:
        return(
          <h1>
            <span className={`${page === 1 ? 'active' : ''}`} onClick={() => setPage(1)}>.</span>
            <span className={`${page === 2 ? 'active' : ''}`} onClick={() => setPage(2)}>.</span>
          </h1>
        )
        case 3:
        return(
          <h1>
            <span className={`${page === 1 ? 'active' : ''}`} onClick={() => setPage(1)}>.</span>
            <span className={`${page === 2 ? 'active' : ''}`} onClick={() => setPage(2)}>.</span>
            <span className={`${page === 3 ? 'active' : ''}`} onClick={() => setPage(3)}>.</span>
          </h1>
        )
      case 4:
        return(
          <h1>
            <span className={`${page === 1 ? 'active' : ''}`} onClick={() => setPage(1)}>.</span>
            <span className={`${page === 2 ? 'active' : ''}`} onClick={() => setPage(2)}>.</span>
            <span className={`${page === 3 ? 'active' : ''}`} onClick={() => setPage(3)}>.</span>
            <span className={`${page === 4 ? 'active' : ''}`} onClick={() => setPage(4)}>.</span>
          </h1>
        )
    }
  }

  return(
    <Container className="dots">
      <Dots />
    </Container>
  )
}

export default DotsPages;