import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import axios from 'axios';
import { connect } from 'react-redux';
import ReactMinimalProgressSteps from 'react-minimal-progress-steps'
import 'react-minimal-progress-steps/dist/index.css'

import { CircularProgressbar } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import Moment from 'react-moment';

import Modal from '../../Components/Modal';
import Button from '../../Components/Button';
import Colors from '../../Components/Colors';
import TextInput from '../../Components/TextInput';
import Select from '../../Components/Select';

import Steps from './Steps';

const Container = styled.div`
  width: auto; 
  margin: 5rem 10rem;
  a{
    text-decoration: none;
  }
  overflow: hidden;
`

const Top = styled.div`
  display: flex;
  align-items: center;
  h1 {
    font-size: 6rem;
    margin: 0rem;
  }
  p{
    font-size: 3.6rem;
    margin: 0.8rem 0rem;
  }
  .button {
    margin-left: 3rem;
    height: 3rem;
  }
`

const Ideas = styled.div`
  background-color: #F8F8F8;
  display: flex;
  align-items: center;
  border-radius: 5px;
  img {
    height: 12rem;
    padding: 2rem;
    flex: 0;
  }
  .text {
    flex: 1;
    padding: 2rem;
    margin-left: 8rem;
    h1 {
      font-size: 3rem;
      margin: 1rem 0rem;
    }
    p {
      font-size: 2.4rem;
      margin: 1rem 0rem;
    }
  }
  .button {
    flex: 0;
    min-width: 30rem;
    padding: 2rem;
  }
`

const Table = styled.div`
  width: 100%;
  overflow-y: auto;
  max-height: 50vh;
  @media screen and (max-width: 1366px){
    max-height: 40vh;
  }
  table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0rem 1rem;
    transform-style: preserve-3d;
    thead {
      font-family: Montserrat-Black;
      transform: translate3d(0, 0, 1px);
      tr {
        height: 10rem;
        th {
          position: sticky;
          top: 0;
          background-color: white;
          min-width: 10%;
          min-height: 5rem;
          font-size: 2rem;
          border-bottom: 3px solid black;
        }
      }
    }
    tbody {
      font-family: Montserrat-Bold;
      transform: translate3d(0, 0, 0);
      tr{
        cursor: pointer;
        background-color: #DDD;
        transition: background-color 0.3s ease;
        
        td{
          text-align: center;
          font-size: 1.8rem;
          max-width: 30rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          height: 5.7rem;
          padding: 0rem 2rem;
          &:first-child {
            border-radius: 10px 0px 0px 10px;
          }
          &:last-child {
            border-radius: 0px 10px 10px 0px;
            max-width: 5rem;
          }
          &:first-child{
            font-family: Montserrat-Black;
          }
          .progress {
            display: flex;
            justify-content: flex-end;
            font-size: 1.6rem;
            align-items: center;
            .progress-bar {
              height: 4rem;
              fill: black;
              display: flex;
              justify-content: flex-end;
              max-width: 4rem;
              margin-right: 2rem;
              .CircularProgressbar-path {
                stroke: #7ED957;
              }
              .CircularProgressbar-trail {
                stroke: #BBB;
              }
            }
            span {
              font-weight: 600;
            }
          }
        }
        &.noProjectsTr{
          background-color: white;
          td {
            text-align: left;
          }
        }    
      }
      .noProjects {
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          flex: 0;
          height: 30rem;
        }
        h1 {
          font-size: 3rem;
        }
        p {
          font-size: 2rem;
          font-family: Montserrat-Light;
        }
      }
    }
  }
`

const NewProject = styled.div`
  *{
    margin: 0;
    padding: 0;
  }
  .top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    h1 {
      font-size: 3rem;
    }
    p {
      font-size: 1.6rem;
      font-family: Montserrat-Bold;
      cursor: pointer;
    }
  }
  .fields {
    display: flex;
    .left {
      padding: 1rem;
      flex: 0.8
    }
    .right {
      padding: 1rem;
      flex: 1;
      display: flex;
      flex-direction: column;
      .formItem {
        flex: 0;
      }
      .buttons {
        flex: 1;
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
        .custom-button {
          max-width: 30rem;
        }
      }
    }
    .formItem {
      padding: 1rem;
    }
    .buttons {
      padding: 1rem;
    }
  }
`

const ProjectDetails = styled.div`
  *{
    margin: 0;
    padding: 0;
  }
  .top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    h1 {
      font-size: 3rem;
    }
    p {
      font-size: 1.6rem;
      font-family: Montserrat-Bold;
      cursor: pointer;
    }
  }
  .data {
    display: flex;
    flex-direction: column;
    .details {
      padding: 2rem;
      display: flex;
      justify-content: space-between;
      h2{
        font-size: 2.4rem;
        margin: 2rem 0rem;
      }
      .current_progress{
        display: flex;
        flex-direction: column;
        align-items: center;
        .progress {
          display: flex;
          justify-content: flex-end;
          font-size: 1.6rem;
          align-items: center;
          .progress-bar {
            height: 8rem;
            fill: black;
            display: flex;
            justify-content: flex-end;
            max-width: 8rem;
            margin-right: 2rem;
            .CircularProgressbar-path {
              stroke: #7ED957;
            }
            .CircularProgressbar-trail {
              stroke: #BBB;
            }
          }
          span {
            font-weight: 600;
            font-size: 2rem;
          }
        }
      }
      .date {
        display: flex;
        flex-direction: column;
        align-items: center;
        .date_content {
          display: flex;
          align-items: center;
          img {
            height: 8rem;
          }
          .date_text {
            padding: 2rem;
            font-size: 2rem;
            .ini_date {
              font-family: Montserrat-MediumItalic;
            }
            .duration {
              font-family: Montserrat-BlackItalic;
            }
          }
        }
      }
      .step {
        display: flex;
        flex-direction: column;
        align-items: center;
        .step_content {
          display: flex;
          align-items: center;
          img {
            height: 8rem;
          }
          .step_text {
            padding: 2rem;
            .step_number {
              font-size: 2rem;
              font-family: Montserrat-MediumItalic;
            }
            .step_description {
              font-size: 1.4rem;
              font-family: Montserrat-MediumItalic;
              max-width: 16rem;
            }
          }
        }
      }
    }
    .description {
      padding: 2rem;
    }
  }
  .buttons {
    display: flex;
    justify-content: space-between;
    .custom-button{
      max-width: 28rem;
    }
  }
`

// Steps
const StepsContainer = styled.div`
  width: auto; 
  margin: 1rem 1rem;
  a{
    text-decoration: none;
  }
  overflow: hidden;
  .button {
    max-width: 30rem;
    margin-bottom: 2rem;
  }
`

const Progress = styled.div`
  display: flex;
  align-items: center;
  max-height: 5rem;
  .button {
    max-width: 20rem;
    margin: 0;
  }
  .progressbar{
    flex: 1;
    padding: 0px 4rem;
    button{
      display: none;
    }
    ._1vzPx{
      margin: 0;
    }
    ._2EqlW{
      background-color: ${props => props.colors.grey};
    }
    ._3PfrK{
      border-color: ${props => props.colors.green};
      background-color: ${props => props.colors.green};
      &.false{
        border-color: #e0e0e0;
        background-color: white;
      }
      &._35Ago{
        border-color: ${props => props.colors.orange};
        background-color: ${props => props.colors.orange};
      }
    }
  }
`

const StepsTitle = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  p {
    font-family: Montserrat-MediumItalic;
    font-size: 2rem;
    margin-bottom: 0.5rem;
  }
  h1 {
    font-family: Montserrat-Bold;
    font-size: 3.6rem;
    margin-top: 0.5rem;
    margin-bottom: 0rem;
  }
`

const StepsContent = styled.div`
  padding: 3rem 10rem;
`

const Projects = ({User, loading, setMenu, showConfirm, showAlert, Project, setProject, setProjectStep}) => {

  let history = useHistory();

  const [modal, setModal] = useState({
    projectDetail: false,
    newProject: false
  })
  const [newProjectForm, setNewProjectForm] = useState({
    nome: '',
    setor: '',
    modelo: '',
    description: ''
  })
  const [projectData, setProjectData] = useState({})
  const [projects, setProjects] = useState([]);
  const [projectDetail, setProjectDetail] = useState({});

  const refresh = () => {
    loading(true);
    axios.get(`${process.env.REACT_APP_API_URL}api/projects/${User.id}`)
      .then(response => { 
        setProjects(response.data);
      })
  }

  useEffect(() => {
    setMenu('projects');
    refresh();
  }, [])
  useEffect(() => {
    loading(false);
  }, [projects])

  const handleIdeasClick = () => {
    history.push('/ideas');
  }
  
  const handleNewProjectClick = () => {
    setModal({...modal, newProject: true})
  }
  const handleCloseNewProject = () => {
    setModal({...modal, newProject: false})
  }

  const handleProjectItemClick = (index) => {
    setProjectDetail(projects[index]);
  }
  const handleCloseProjectDetails = () => {
    setProjectDetail({});
  }
  useEffect(() => {
    console.log(projectDetail);
    console.log('Entrou');
    if(Object.keys(projectDetail).length === 0) setModal({...modal, projectDetail: false});
    else setModal({...modal, projectDetail: true})
  }, [projectDetail]);

  const handleDeleteProject = async (project_id) => {
    showConfirm({
      title: 'Excluir Projeto',
      message: 'Ao excluir um projeto, você perderá todo o progresso e ele será apagado da nossa base de dados. Deseja prosseguir?',
      onYes: () => {
        axios.get(`${process.env.REACT_APP_API_URL}api/projects/delete/${project_id}`)
        .then(response => {
          if (response.status === 200){
            showAlert({
              title: 'Projeto excluido',
              message: 'Seu projeto foi excluído.',
              callback: () => {
                setProjectDetail({});
                refresh();
              }
            })
          }
        })
      }
    })
  }

  const handleProjectProgressClick = () => {
    setProject(projectDetail);
  }

  const MyProjects = () => {
    return (
      <Container>
        <Modal visible={modal.newProject}>
          <NewProject>
            <div className="top">
              <h1>Iniciar Projeto</h1>
              <p className="close" onClick={handleCloseNewProject}>X</p>
            </div>
            <div className="fields">
              <div className="left">
                <div className="formItem">
                  <TextInput label='Nome' size={1.3} value={newProjectForm.nome} onChange={val => setNewProjectForm({...newProjectForm, nome: val})}/>
                </div>
                <div className="formItem">
                  <Select caption="Setor" size={1.3} backgroundColor="white"/>
                </div>
                <div className="formItem">
                  <Select caption="Modelo de Negócios" size={1.3} backgroundColor="white"/>
                </div>
              </div>
              <div className="right">
                <div className="formItem">
                  <TextInput label="Descrição" size={1.3} multiline={true}/>
                </div>
                <div className="buttons">
                  <Button caption="Iniciar Projeto" color={Colors.green}/>
                </div>
              </div>
            </div>
          </NewProject>
        </Modal>
        <Modal visible={modal.projectDetail} className="detailOverlay">
          <ProjectDetails>
            <div className="top">
              <h1>{projectDetail.nome || `Ideia ${projectDetail.idea_id}`}</h1>
              <p className="close" onClick={handleCloseProjectDetails}>X</p>
            </div>
            <div className="data">
              <div className="details">
                <div className="current_progress">
                  <h2>Progresso Atual</h2>
                  <div className="progress">
                    <CircularProgressbar value={projectDetail.progress} className="progress-bar" strokeWidth={16} /> 
                    <span>{projectDetail.progress}%<br />concluído</span>
                  </div>
                </div>
                <div className="date">
                  <h2>Data de Início</h2>
                  <div className="date_content">
                    <img src="images/icons/calendario-com-quadrados.png" alt="" />
                    <div className="date_text">
                      <p className="ini_date"><Moment format="DD/MM/YYYY">{projectDetail.start_date}</Moment></p>
                      <p className="duration"><Moment date={projectDetail.start_date} format="DD" durationFromNow /> Dias</p>
                    </div>
                  </div>
                </div>
                <div className="step">
                  <h2>Etapa Atual</h2>
                  <div className="step_content">
                  <img src="images/icons/layers.png" alt="" />
                    <div className="step_text">
                      <p className="step_number">{projectDetail.step}º Passo</p>
                      <p className="step_description">{projectDetail.step_name}</p>
                    </div>
                  </div>
                </div>
              </div>
              <div className="description">
                <TextInput label="Descrição" value={projectDetail.description} size={1.3} multiline={true} disabled={true}/>
              </div>
            </div>
            <div className="buttons">
              <Button caption="Excluir Projeto" color={Colors.gray} icon='trash' onClick={() => handleDeleteProject(projectDetail.project_id)}/>
              <Button caption="Retomar Projeto" color={Colors.green} onClick={handleProjectProgressClick}/>
            </div>
          </ProjectDetails>
        </Modal>
        
        <Top>
          <h1>Meus Projetos</h1>
          <div className="button">
            <Button caption="+ Novo Projeto" color={Colors.green} type="round" onClick={handleNewProjectClick}/>
          </div>
        </Top>
        <Ideas>
          <img src="images/icons/solution.png" alt="" />
          <div className="text">
            <h1>Está sem ideias de negócios?</h1>
            <p>Temos centenas de ideias para te inspirar a criar seu novo<br />projeto! Acesse clicando no botão ao lado</p>
          </div>
          <div className="button">
            <Button caption="Ver Ideias" color={Colors.orange} type="round" icon="bulb" size={1.3} onClick={handleIdeasClick} />
          </div>
        </Ideas>
        <Table>
          <table>
            <thead>
              <tr>
                <th>Nome</th>
                <th>Setor</th>
                <th>Data Início</th>
                <th>Nota</th>
                <th></th>
              </tr>
            </thead>
            {projects.length === 0 ?
              <tbody>
                <tr className="noProjectsTr">
                  <td colSpan="5">
                    <div className="noProjects">
                      <img src="images/icons/rocket.png" alt="" />
                      <div className="text">
                        <h1>Você ainda não iniciou nenhum projeto!</h1>
                        <p>Temos centenas de ideias de negócios que você pode usar<br />
                        para começar seu negócio. Além disso, você também pode<br />
                        começar um projeto com uma ideia que você teve!
                        </p>
                      </div>
                    </div>
                  </td>
                </tr>
              </tbody>
              :
              <tbody>
                {projects.map((project, index) => {
                  return (
                    <tr onClick={() => handleProjectItemClick(index)}>
                      <td>{project.nome || `Ideia ${project.idea_id}`}</td>
                      <td>{project.setor}</td>
                      <td><Moment format="DD/MM/YYYY">{project.start_date}</Moment></td>
                      <td>7,4</td>
                      <td>
                        <div className="progress">
                          <CircularProgressbar value={project.progress} className="progress-bar" strokeWidth={16} /> 
                          <span>{project.progress}% concluido</span>
                        </div>
                      </td>
                    </tr>
                  )
                })}
              </tbody>
            }
          </table>
        </Table>
      </Container>
    )
  }

  const ProjectSteps = () => {
    
    useEffect(() => {
      console.log('Project: ',Project);
    }, [])

    const data = [
      { id: 1 },
      { id: 2 },
      { id: 3 },
      { id: 4 },
      { id: 5 },
      { id: 6 },
      { id: 7 },
      { id: 8 },
      { id: 9 },
      { id: 10 },
      { id: 11 },
      { id: 12 },
      { id: 13 },
      { id: 14 },
      { id: 15 },
    ]
    
    const [steps, setSteps] = useState([]);

    useEffect(() => {
      axios.get(`${process.env.REACT_APP_API_URL}api/steps`)
        .then(response => {
          if(response.status === 200) {
            setSteps(['dummy', ...response.data]);
          }
        })
      
    }, []);
    const handlePreviousStep = () => {
      if(Project.step > 1) {
        setProjectStep(Project.step-1);
      }
    }
    const handleNextStep = () => {
      if(Project.step < 15){
        setProjectStep(Project.step+1);
      } 
    }

    return (
      <StepsContainer>
        <div className="button">
          <Button caption="Voltar para  meus projetos" color={Colors.orange} onClick={() => {setProject({type: 'CLEAR_PROJECT'});}}/>
        </div>
        <Progress colors={Colors}>
          <div className="button">
            <Button caption="Etapa Anterior" color={Colors.orange} onClick={handlePreviousStep}/>
          </div>
          <div className="progressbar">
            <ReactMinimalProgressSteps
              data={data}
              selectedItemId={Project.step} // send an item id as props
            />
          </div>
          <div className="button">
            <Button caption="Próxima Etapa" color={Colors.green} onClick={handleNextStep}/>
          </div>
        </Progress>
          {steps.length > 0 &&
            <StepsTitle>
                <p>{steps[Project.step].id || ''}º Passo</p>
                <h1>{steps[Project.step].name || ''}</h1>
            </StepsTitle>
          }
        <StepsContent>
          <Steps />
        </StepsContent>
      </StepsContainer>
    )
  }

  return (
    <div>
      {Project.id !== 0 ?
        <ProjectSteps />
      :
        <MyProjects />
      }

    </div>
  )
}

const mapStateToProps = (state) => ({
  User: state.UserReducer,
  Project: state.ProjectReducer
});
const mapDispatchToProps = (dispatch) => ({
  loading: (status) => dispatch({type: (status ? 'SHOW_LOADING': 'HIDE_LOADING')}),
  setMenu: (menu) => dispatch({type: 'CHANGE_MENU', active: menu}),
  showAlert: ({title = '', message = '', callback}) => dispatch({type: 'SHOW_ALERT', title: title, message: message, callback: callback}),
  showConfirm: ({title = '', message = '', onYes, onNo}) => dispatch({type: 'SHOW_CONFIRM', title: title, message: message, onYes: onYes, onNo: onNo}),
  setProject: (props = {id: 0, step: 0}) => dispatch({type: 'SET_PROJECT', ...props}),
  setProjectStep: (step) => dispatch({type: 'SET_PROJECT_STEP', step: step})
});

export default connect(mapStateToProps, mapDispatchToProps)(Projects);