import React from 'react';
import styled from 'styled-components';

import { CircularProgressbar } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';

import { connect } from 'react-redux';

import Colors from '../../Components/Colors';
import Button from '../../Components/Button';
import { Link, useHistory } from 'react-router-dom';



const Container = styled.div`
  width: auto; 
  margin: 5rem 10rem;
  a{
    text-decoration: none;
  }
  @media screen and (max-width: 768px){
    margin: 1rem 1rem;
  }
`

const Top = styled.div`
    h1 {
      font-size: 6rem;
      margin: 0rem;
    }
    p{
      font-size: 3.6rem;
      margin: 0.8rem 0rem;
    }
    @media screen and (max-width: 768px){
      p{
        font-size: 2.4rem;
      }
    }
    
`

const Tables = styled.div`
  display: flex;
  flex-flow: wrap;
  div {
    flex: 1;
    min-width: 50%;
    &:nth-child(1){
      min-width: 60%;
    }
    &:nth-child(2){
      min-width: 40%;
    }
    &:nth-child(3){
      min-width: 100%;
    }
  }
  @media screen and (max-width: 768px){
    div {
      min-width: 50%;
      &:nth-child(1){
        min-width: 100%;
      }
      &:nth-child(2){
        min-width: 100%;
      }
      &:nth-child(3){
        min-width: 100%;
      }
    }
  }
`

const Table = styled.div`

  .title {
    display: flex;
    align-items: center;
    h2 {
      font-size: 4rem;
      margin-bottom: 0;
      margin-top: 0.8rem;
    }
    .custom-button {
      min-width: 0px;
      max-width: 20rem;
      margin-left: 5rem;
      margin-top: 1rem
    }
  }
  ul {
    padding: 0;
    li{
      display: block;
      width: calc(100% - 5rem);
      background-color: #DDD;
      margin: 0.5rem 0rem;
      padding: 0.8rem;
      border-radius: 5px;
      display: flex;
      align-items: center;
      max-height: 5rem;
      .name {
        font-size: 2.8rem;
        margin: 0;
        flex: 1;
      }
      .progress {
        flex: 0;
        display: flex;
        justify-content: flex-end;
        font-size: 1.6rem;
        align-items: center;
        .progress-bar {
          height: 4rem;
          fill: black;
          display: flex;
          justify-content: flex-end;
          max-width: 3.6rem;
          margin-right: 2rem;
          .CircularProgressbar-path {
            stroke: #7ED957;
          }
          .CircularProgressbar-trail {
            stroke: #BBB;
          }
        }
        span {
          font-weight: 600;
        }
      }
      
    }
  }
  .more {
    display: flex;
    justify-content: center;
    width: calc(100% - 5rem);
    margin: 1rem 0rem;
    .button {
      min-width: 0;
      max-width: 150px;

    }
  }
  @media screen and (max-width: 768px){
    .title{
      h2 {
        font-size: 3rem;
      }
    }
    ul{
      li{
        width: 100%;
        padding: 0;
        max-height: 5rem;
        .name{
          font-size: 2.2rem;
          padding: 2rem;
        }
        .progress {
          min-width: 0px;
          svg {
            min-width: 40px;
          }
        }

      }
    }
  }
`



const Home = ({User, setMenu}) => {
  
  const history = useHistory();
  
  const handleClickMore = (menu) => {
    history.push(menu);
    setMenu(menu)
  }
  
  return(
    <Container>
      <Top>
        <h1>Olá, {User.name.split(' ')[0]}.</h1>
        <p>O melhor dia para começar sua empresa foi ontem, mas o segundo melhor é hoje.<br /> Ao trabalho!</p>
      </Top>
      <Tables>
        <Table className="projects">
          <div className="title">
            <h2><Link to="/projects" onClick={() => setMenu('projects')}>Meus Projetos</Link></h2>
            <Button caption="+ Novo Projeto" type="round" color={Colors.green}/>
          </div>
          <ul>
            <li>
              <h2 className="name">Projeto 1</h2>
              <div className="progress">
                <CircularProgressbar value={20} className="progress-bar" strokeWidth={16} /> 
                <span>20% concluido</span>
              </div>
            </li>
            <li>
              <h2 className="name">Projeto 2</h2>
              <div className="progress">
                <CircularProgressbar value={90} className="progress-bar" strokeWidth={16} /> 
                <span>90% concluido</span>
              </div>
            </li>
            <li>
              <h2 className="name">Projeto 3</h2>
              <div className="progress">
                <CircularProgressbar value={66} className="progress-bar" strokeWidth={16} /> 
                <span>66% concluido</span>
              </div>
            </li>
          </ul>
          <div className="more">
            <div className="button">              
              <Button onClick={() => {handleClickMore('projects')}} caption="Ver Mais" arrow={true} color={Colors.orange}/>
            </div>
          </div>
        </Table>
        <Table className="ideas">
          <div className="title">
            <h2><Link to="/ideas" onClick={() => setMenu('ideas')}>Lista de Ideias</Link></h2>
            <Button caption="+ Nova Ideia" type="round" color={Colors.green}/>
          </div>
          <ul>
            <li>
              <h2 className="name">Projeto 1</h2>
              <div className="progress">
                <CircularProgressbar value={20} className="progress-bar" strokeWidth={16} /> 
                <span>20% concluido</span>
              </div>
            </li>
            <li>
              <h2 className="name">Projeto 2</h2>
              <div className="progress">
                <CircularProgressbar value={90} className="progress-bar" strokeWidth={16} /> 
                <span>90% concluido</span>
              </div>
            </li>
            <li>
              <h2 className="name">Projeto 3</h2>
              <div className="progress">
                <CircularProgressbar value={66} className="progress-bar" strokeWidth={16} /> 
                <span>66% concluido</span>
              </div>
            </li>
          </ul>
          <div className="more">
            <div className="button">
              <Button onClick={() => {handleClickMore('ideas')}} caption="Ver Mais" arrow={true} color={Colors.orange}/>
            </div>
          </div>
        </Table>
        <Table className="">
          <div className="title">
            <h2><Link to="/reports" onClick={() => setMenu('reports')}>Relatórios</Link></h2>
          </div>
          <ul>
            <li>
              <h2 className="name">Projeto 1</h2>
              <div className="progress">
                <CircularProgressbar value={20} className="progress-bar" strokeWidth={16} /> 
                <span>20% concluido</span>
              </div>
            </li>
          </ul>
        </Table>
      </Tables>
    </Container>
  )
}

const mapStateToProps = (state) => ({
  User: state.UserReducer
})

const mapDispatchToProps = (dispatch) => ({
  setMenu: (menu) => dispatch({type: 'CHANGE_MENU', active: menu})
})

export default connect(mapStateToProps, mapDispatchToProps)(Home);