const INITIAL_STATE = {
    visible: true
};

const LoadingReducer = (state = INITIAL_STATE, action) => {
  switch (action.type){
    case 'SHOW_LOADING':
      return {
        visible: true
      };
    case 'HIDE_LOADING':
      return {
        visible: false
      };
    default:
      return state;
  }
}

export default LoadingReducer;