import React, { useEffect, useRef, useState } from 'react';
import { connect } from 'react-redux';

import styled from 'styled-components';
import {
  useHistory,
  Switch,
  Route,
  Link
} from "react-router-dom";

import {ReactComponent as UserLogo} from '../Components/icons/user-circle_1.svg';
import {ReactComponent as ConfigLogo} from '../Components/icons/cogs.svg';
import {ReactComponent as ExitLogo} from '../Components/icons/exit.svg';
import {ReactComponent as AdminLogo} from '../Components/icons/user-admin.svg';
import Home from './Home';
import Ideas from './Ideas';
import Projects from './Projects';
import Reports from './Reports';
import Admin from './Admin';

const Container = styled.div`
  height: 100vh;
  width: 100vw;
  font-size: 10px;
  @media screen and (max-width: 1366px){
    font-size: 8px;
  }
  .main {
    height: 100%;
    display: flex;
    flex-direction: row;
    background-color: white;
  }
`

const Header = styled.div`
  display: flex;
  position: sticky;
  height: 5em;
  background-color: black;
  align-items: center;
  color: white;
  padding: 1em 0em;
  overflow: hidden;
  div {
    flex: 1;
    display: flex;
    justify-content: center;
    &.menu {
      justify-content: flex-start;
    }
    &.actions {
      justify-content: flex-end;
      .user-menu {
        position: fixed;
        display: flex;
        flex-direction: column;
        left: 0px;
        top: 7rem;
        width: 100vw;
        height: 100vh;
        color: black;
        background-color: rgba(0,0,0,0.7);
        display: none;
        z-index: 10;
        .menu {
          position: absolute;
          right: 5rem;
          top: 1rem;
          display: flex;
          flex-direction: column;
          background-color: white;
          width: fit-content;
          border-radius: 10px;
          ul{
            list-style-type: none;
            padding: 1rem;
            margin: 0;
            li{
              padding: 1rem;
              font-family: Montserrat-Medium;
              font-size: 1.6rem;
              margin: 1rem 0rem;
              display: flex;
              align-items: center;
              cursor: pointer;
              border-radius: 5px;
              h3 {
                margin: 0rem;
                font-size: 2.6rem;
                font-family: Montserrat-Black;
              }
              svg {
                height: 3rem;
                width: 3rem;
                padding: 0.5rem;
                margin-right: 1.4rem;
              }
              &:hover {
                background-color: #DDD;
              }
              &:first-child {
                cursor: default;
                &:hover {
                  background-color: white;
                }
              }
            }
          }
        }
        &.visible {
          display: block;
        }

      }
    }
    &.menu, 
    &.actions {
      display: flex;
      flex-direction: row;
      align-items: center;
      img, svg {
        height: 5em;
        width: auto;
        padding: 0.3em 2em;
        cursor: pointer;
      }
    }
    &.logo {
      display: flex;
      flex-direction: row;
      align-items: center;
      img{
        height: 6em;
        width: auto;
        padding: 0em;
        cursor: pointer;
      }
    }
    @media screen and (max-width: 768px){
      &.logo {
        img{
          padding: 0.3em;
        }
      }
      &.menu, 
      &.actions {
        display: flex;
        flex-direction: row;
        align-items: center;
        img, svg {
          height: 4em;
          width: auto;
          padding: 0.3em 2em;
          cursor: pointer;
        }
      }
    }

  }

`

const Menu = styled.div`
  flex: 0;
  display: flex;
  flex-direction: column;
  padding: 2em 0em;
  width: fit-content;
  max-width: 8em;
  transition: all 0.5s ease-out;
  background: #F8F8F8;
  height: 100vh;
  div{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    padding: 2em;
    overflow: hidden;
    transition: all 0.3s ease-out;
    img, svg {
      min-width: 4em;
      height: 4em;
      width: 4em;
    }
    h1{
      font-family: Montserrat-Black;
      white-space: nowrap;
      font-size: 2em;
      margin-left: 2em;
    }
    &.selected,
    &:hover { 
      background: #DDD;
    }
  }
  &.expanded {
    max-width: 45em;
  }
  @media screen and (max-width: 768px){
    max-width: 0rem;
    div{
      padding: 0;
    }
    &.expanded {
      max-width: 90vw;
      div{
        padding: 2em;
      }
    }
  }
`

const Content = styled.div`
  flex: 1;
  display: flex;
  flex-flow: column;
  overflow-y: scroll;
  .menuOverlay {
    position: absolute;
    opacity: 0;
    background: rgba(0, 0, 0, 0.7);
    width: 0vw;
    height: 0vh;
    min-width: 0vw;
    min-height: 100vh;
    transition: all 0.5s ease-out;
    z-index: 10;
    &.expanded{
      min-width: 100vw;
      opacity: 1;
    }
  }
  .userMenuOverlay {
    position: fixed;
    display: flex;
    flex-direction: column;
    left: 0px;
    top: 7rem;
    width: 100vw;
    height: 100vh;
    color: black;
    background-color: rgba(0,0,0,0.7);
    display: none;
    z-index: 10;
    .menu {
      position: absolute;
      right: 5rem;
      top: 1rem;
      display: flex;
      flex-direction: column;
      background-color: white;
      width: fit-content;
      border-radius: 10px;
      ul{
        list-style-type: none;
        padding: 1rem;
        margin: 0;
        li{
          padding: 1rem;
          font-family: Montserrat-Medium;
          font-size: 1.6rem;
          margin: 1rem 0rem;
          display: flex;
          align-items: center;
          cursor: pointer;
          border-radius: 5px;
          h3 {
            margin: 0rem;
            font-size: 2.6rem;
            font-family: Montserrat-Black;
          }
          svg {
            height: 3rem;
            width: 3rem;
            padding: 0.5rem;
            margin-right: 1.4rem;
          }
          &:hover {
            background-color: #DDD;
          }
          &:first-child {
            cursor: default;
            &:hover {
              background-color: white;
            }
          }
        }
      }
    }
    &.visible {
      display: block;
    }
  }
  @media screen and (max-width: 768px){
    min-width: 100vw;
  }
`

const Main = ({activeMenu, setMenu, logoutConfirm, User}) => {

  const history = useHistory();
  const menuRef = useRef();
  const menuOverlayRef = useRef();


  const [userMenu, setUserMenu] = useState(false);

  useEffect(() => {
    console.log(User);
  }, [])

  const handleMenuClick = () => {
    menuRef.current.classList.toggle('expanded');
    menuOverlayRef.current.classList.toggle('expanded');
  }

  const handleTabClick = (evt) => {
    

    menuRef.current.classList.remove('expanded');
    menuOverlayRef.current.classList.remove('expanded');
    setMenu(evt.currentTarget.dataset.page);
    history.push(evt.currentTarget.dataset.page);
  }

  const handleLogout = () => {
    logoutConfirm();
  }

  return (
    <Container>
      <Header>
        <div className="menu">
          <img onClick={handleMenuClick} src="images/icons/botao-de-menu.png" title="Expandir Menu" />
        </div>
        <div className="logo">
          <Link to="/" onClick={() => setMenu('home')}><img src="images/businesspal_logo.png" alt="" /></Link>
        </div>
        <div className="actions">
          <UserLogo fill="white" onClick={() => setUserMenu(!userMenu)}/>
        </div>
      </Header>
      <div className="main">
        <Menu ref={menuRef} className="noselect">
          <div className={activeMenu === 'home' ? 'selected' : ''} data-page="home" onClick={handleTabClick}>
            <img src="images/icons/home.png" alt="" />
            <h1>Página Inicial</h1>
          </div>
          <div className={activeMenu === 'ideas' ? 'selected' : ''} data-page="ideas" onClick={handleTabClick}>
            <img src="images/icons/bulb.svg" alt="" />
            <h1>Idéias de Negócios</h1>
          </div>
          <div className={activeMenu === 'projects' ? 'selected' : ''}  data-page="projects" onClick={handleTabClick}>
            <img src="images/icons/layers.png" alt="" />
            <h1>Meus Projetos</h1>
          </div>
          <div className={activeMenu === 'reports' ? 'selected' : ''}  data-page="reports" onClick={handleTabClick}>
            <img src="images/icons/report.png" alt="" />
            <h1>Relatórios</h1>
          </div>
          <div className={activeMenu === 'support' ? 'selected' : ''}  data-page="support" onClick={handleTabClick}>
            <img src="images/icons/information.png" alt="" />
            <h1>Suporte</h1>
          </div>
          {User.is_admin && 
            <div className={activeMenu === 'admin' ? 'selected' : ''}  data-page="admin" onClick={handleTabClick}>
              <AdminLogo />
              <h1>Admin</h1>
            </div>
          }
        </Menu>
        <Content>
          <div ref={menuOverlayRef} className="menuOverlay"></div>
          <div className={`userMenuOverlay ${userMenu ? 'visible' : ''}`} onClick={() => setUserMenu(false)}>
            <div className="menu">
              <ul>
                <li><h3>Configurações</h3></li>
                <li><ConfigLogo />Configurações da Conta</li>
                <li onClick={handleLogout}><ExitLogo />Sair da Conta</li>
              </ul>
            </div>
          </div>
          <Switch>
            <Route path="/ideas" >
              <Ideas />
            </Route>
            <Route path="/projects" >
              <Projects />
            </Route>
            <Route path="/reports" >
              <Reports />
            </Route>
            <Route path="/support" >
              <h1>Support</h1>
            </Route>
            <Route path="/admin" >
              <Admin />
            </Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
        </Content>
      </div>
    </Container>
  )
}

const mapStateToProps = (state) => ({
  activeMenu: state.MenuReducer.active,
  User: state.UserReducer
});
const mapDispatchToProps = (dispatch) => ({
  setMenu: (menu) => dispatch({type: 'CHANGE_MENU', active: menu}),
  logoutConfirm: () => dispatch({
    type: 'SHOW_CONFIRM', 
    title: 'Logout',
    message: 'Deseja realmente fazer logout?',
    onYes: () => {
      localStorage.removeItem('isLogedIn');
      document.location.reload();
    },
    visible: true
  })
});

export default connect(mapStateToProps, mapDispatchToProps)(Main);