import React from 'react';
import { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import axios from 'axios';
import ReactTooltip from 'react-tooltip';

import { ReactComponent as Like } from '../../../Components/icons/like.svg'
import { ReactComponent as Star } from '../../../Components/icons/star-outline.svg'

import Counter from '../../../Components/Counter';
import Button from '../../../Components/Button';
import Colors from '../../../Components/Colors';
import Modal from '../../../Components/Modal';
import Select from '../../../Components/Select';
import TextInput from '../../../Components/TextInput';

const Container = styled.div`
  animation: appear 0.5s forwards;
`

const Top = styled.div`
  display: flex;
  align-items: center;
  h1 {
    font-size: 5rem;
    margin: 0rem;
  }
`
const Content = styled.div`
  width: 100%;

  .counters {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
    margin-top: 2rem;
    .counter{
      flex: 1;
    }  
  }
  .buttons{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10rem;
    margin-top: 2rem;
    .custom-button {
      max-width: 30rem;
    }
  }
`

const ModalForm = styled.div`
  display: flex;
  flex-direction: column;
  .top {
    display: flex;
    justify-content: space-between;
    .title {
      font-size: 3.6rem;
      font-family: Montserrat-Black;
    }
    .close {
      font-size: 2rem;
      font-family: Montserrat-Medium;
      padding: 0rem 2rem 2rem 0rem;
      cursor: pointer;
    }
    svg {
      width: 3rem;
      padding: 1rem;
      cursor: pointer;
      &.thumbs-down {
        transform: rotate(180deg)
      }
    }
  }
  .content {
    display: flex;
    .left {
      flex: 0.8;
    }
    .right {
      flex: 1;
      display: flex;
      flex-direction: column;
    }
    .input {
      margin: 1rem;
    }
    .textarea {
      margin: 1rem;
      height: 15rem;
    }
    .buttons {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      margin: 1rem;
      /* margin-left: 15rem;
      @media screen and (max-width: 1366px){
        margin-left: 10rem;
      } */
      .custom-button {
        width: fit-content;
        margin-left: 1rem;
        max-height: 5rem;
      }
    }
  }
`

const Table = styled.div`
  width: 100%;
  overflow-y: auto;
  max-height: 50vh;
  @media screen and (max-width: 1366px){
    max-height: 40vh;
  }
  table {
    width: 100%;
    border-spacing: 0px;
    transform-style: preserve-3d;
    thead {
      font-family: Montserrat-Black;
      transform: translate3d(0, 0, 1px);
      tr {
        height: 10rem;
        th {
          position: sticky;
          top: 0;
          background-color: white;
          min-width: 10%;
          min-height: 5rem;
          font-size: 2rem;
          border-bottom: 3px solid black;
        }
      }
    }
    tbody {
      font-family: Montserrat-Bold;
      transform: translate3d(0, 0, 0);
      tr{
        cursor: pointer;
        background-color: white;
        transition: background-color 0.3s ease;
        td{
          text-align: center;
          font-size: 1.8rem;
          max-width: 30rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          height: 5.7rem;
          &.actions{
            display: flex;
            .action {
              display: flex;
              flex-direction: column;
              svg {
                width: 3rem;
                padding: 1rem;
                &.thumbs-down {
                  transform: rotate(180deg)
                }
              }
            }
          }
          svg {
            width: 3rem;
            padding: 1rem;
            &.thumbs-down {
              transform: rotate(180deg)
            }
          }
          &:first-child{
            font-family: Montserrat-Black;
          }
        }
        &:hover {
          background-color: #DDD;
        }
      }
    }
  }
`

const AdminIdeas = ({showAlert, showConfirm, loading}) => {

  const setores = [
    {value: 'Financeiro', caption: 'Financeiro'},
    {value: 'Saúde e Fitness', caption: 'Saúde e Fitness'},
    {value: 'Educação', caption: 'Educação'},
    {value: 'Varejo', caption: 'Varejo'},
    {value: 'Recursos Humanos', caption: 'Recursos Humanos'},
    {value: 'Serviços Empresariais', caption: 'Serviços Empresariais'},
    {value: 'Marketing', caption: 'Marketing'},
    {value: 'Mobilidade e Transportes', caption: 'Mobilidade e Transportes'},
    {value: 'Alimentação', caption: 'Alimentação'},
    {value: 'Logística', caption: 'Logística'},
    {value: 'Construção Civil', caption: 'Construção Civil'},
    {value: 'Esportes', caption: 'Esportes'},
    {value: 'Agricultura e Pecuária', caption: 'Agricultura e Pecuária'},
    {value: 'Eventos', caption: 'Eventos'},
    {value: 'TI', caption: 'TI'},
    {value: 'Governo', caption: 'Governo'},
    {value: 'Serviços Residenciais', caption: 'Serviços Residenciais'},
    {value: 'Turismo', caption: 'Turismo'},
    {value: 'PET', caption: 'PET'},
    {value: 'Jurídico', caption: 'Jurídico'},
    {value: 'Beleza e Cosméticos', caption: 'Beleza e Cosméticos'},
    {value: 'Energia e Manejo de Resíduos', caption: 'Energia e Manejo de Resíduos'},
    {value: 'Música e Artes', caption: 'Música e Artes'},
    {value: 'Relacionamentos', caption: 'Relacionamentos'},
    {value: 'Imobiliário', caption: 'Imobiliário'}
  ];

  const classificar = [
    {value: 'conhecimento', caption: 'Conhecimentos Específicos'},
    {value: 'tecnologia', caption: 'Tecnologia Necessária'},
    {value: 'investimento', caption: 'Investimento Inicial'},
  ]

  const conhecimentos = [
    {value: 1, caption: 'Muito Baixa'},
    {value: 2, caption: 'Baixa'},
    {value: 3, caption: 'Média'},
    {value: 4, caption: 'Alta'},
    {value: 5, caption: 'Muito Alta'}
  ]
  const tecnologias = [
    {value: 1, caption: 'Muito Baixa'},
    {value: 2, caption: 'Baixa'},
    {value: 3, caption: 'Média'},
    {value: 4, caption: 'Alta'},
    {value: 5, caption: 'Muito Alta'}
  ]

  const newFormDefault = {
    setor: '',
    modelo: '',
    conhecimento: '',
    tecnologia: '',
    investimento: '',
    description: ''
  }

  const fieldInvestimento = (val) => {
    switch (val) {
      case 1:
        return '$';
        break;
      case 2:
        return '$$';
        break;
      case 3:
        return '$$$';
        break;
      case 4:
        return '$$$$';
        break;
      case 5:
        return '$$$$$';
        break;
    }
  }
  const fieldIntensity = (val) => {
    switch (val) {
      case 1:
        return 'Muito Baixa';
        break;
      case 2:
        return 'Baixa';
        break;
      case 3:
        return 'Média';
        break;
      case 4:
        return 'Alta';
        break;
      case 5:
        return 'Muito Alta';
        break;
    }
  }

  const [ideasCounter, setIdeasCounter] = useState(0);
  const [ideasOwnerCounter, setIdeasOwnerCounter] = useState(0);
  const [ideasSavedCounter, setIdeasSavedCounter] = useState(0);
  const [ideasPerUserCounter, setIdeasPerUserCounter] = useState(0);
  const [ideaList, setIdeaList] = useState([]);
  const [newItemModal, setNewItemModal] = useState(false);
  const [formNewItem, setFormNewItem] = useState({
    nome: '',
    setor: '',
    modelo: '',
    conhecimento: '',
    tecnologia: '',
    investimento: '',
    description: ''
  });

  useEffect(() => {
    ReactTooltip.rebuild()
  },[ideaList])

  const refresh = async () => {
    loading(true);
    let ideas = axios.get(`${process.env.REACT_APP_API_URL}api/ideas`);
    let users = axios.get(`${process.env.REACT_APP_API_URL}api/users/count`);
    let userIdeas = axios.get(`${process.env.REACT_APP_API_URL}api/ideas/count-users`);
    let savedIdeas = axios.get(`${process.env.REACT_APP_API_URL}api/ideas/count-saved`);
    Promise.all([users, ideas, userIdeas, savedIdeas])
      .then(([users, ideas, userIdeas, savedIdeas]) => {
        let ideasPerUser = Math.round(userIdeas.data[0].count / users.data[0].count)
        setIdeaList(ideas.data);
        setIdeasCounter(ideas.data.length);
        setIdeasOwnerCounter(ideas.data.filter(item => item.owner !== null).length);
        setIdeasSavedCounter(savedIdeas.data[0].count);
        setIdeasPerUserCounter(ideasPerUser);
        loading(false);
      })
  }

  useEffect(() => {
    refresh();
  }, []);

  const handleSaveNewIdea = async () => {
    let error = Object.keys(formNewItem).some(item => formNewItem[item] === '')
    if (error) return showAlert({title: 'Preenchimento Incorreto', message: 'Todos os campos são obrigatórios'});
    let response = await axios.post(`${process.env.REACT_APP_API_URL}api/ideas/create`, {data: formNewItem});
    if(response.status === 200){
      showAlert({
        title: 'Ideia Adicionada',
        message: 'Nova idéia criada com sucesso.',
        callback: () => {
          setNewItemModal(false)
          setFormNewItem(newFormDefault);
          refresh();
        }
      })
    }
  }

  const handleIdeaClick = async () => {

  }

  return (
    <Container>
      <Modal visible={newItemModal}>
        <ModalForm>
          <div className="top">
            <h1 className="title">Nova Ideia</h1>
            <h3 className="close noselect" onClick={() => {
              setNewItemModal(false)
              setFormNewItem(newFormDefault);
            }}>X</h3>
          </div>
          <div className="content">
            <div className="left">
              <div className="input">
                <TextInput size={1.3} label='Nome' value={formNewItem.nome} onChange={val => setFormNewItem({...formNewItem, nome: val})}/>
              </div>
              <div className="input">
                <Select size={1.3} caption='Setor' backgroundColor="white" value={formNewItem.setor} options={setores} onChange={val => setFormNewItem({...formNewItem, setor: val})} />
              </div>
              <div className="input">
                <TextInput size={1.3} label='Modelo de Receita' value={formNewItem.modelo} onChange={val => setFormNewItem({...formNewItem, modelo: val})}/>
              </div>
              <div className="input">
                <Select size={1.3} caption='Tecnologia Necessária' backgroundColor="white" value={formNewItem.tecnologia} options={conhecimentos} onChange={val => setFormNewItem({...formNewItem, tecnologia: val})} />
              </div>
              <div className="input">
                <Select size={1.3} caption='Conhecimentos Específicos' backgroundColor="white" value={formNewItem.conhecimento} options={conhecimentos} onChange={val => setFormNewItem({...formNewItem, conhecimento: val})} />
              </div>
            </div>
            <div className="right">
              <div className="input">
                <Select size={1.3} caption='Investimento Inicial' backgroundColor="white" value={formNewItem.investimento} options={conhecimentos} onChange={val => setFormNewItem({...formNewItem, investimento: val})} />  
              </div>
              <div className="textarea">
                <TextInput size={1.3} label='Descrição' value={formNewItem.description} onChange={val => setFormNewItem({...formNewItem, description: val})} multiline={true}/>
              </div>
              <div className="buttons">
                <Button color={Colors.green} caption="Salvar Ideia" onClick={handleSaveNewIdea}/>
              </div>
            </div>
          </div>
        </ModalForm>
      </Modal> 
      <ReactTooltip place="right"/>
      <Top>
        <h1>Ideias de Negócio</h1>
      </Top>
      <Content>
        <div className="counters">
          <div className="counter"><Counter value={ideasCounter} caption="Ideias de Negócios"/></div>
          <div className="counter"><Counter value={ideasOwnerCounter} caption="Ideias Adicionadas"/></div>
          <div className="counter"><Counter value={ideasSavedCounter} caption="Ideias Salvas"/></div>
          <div className="counter"><Counter value={ideasPerUserCounter} caption="Ideias por Pessoa"/></div>
        </div>
        <div className="buttons">
          <Button caption="Adicionar Ideias" color={Colors.green} onClick={() => setNewItemModal(true)}/>
          <Button caption="Adicionar lote de Ideias" color={Colors.orange}/>
        </div>
        <Table tab={'list'}>
          <table>
            <thead>
              <tr>
                <th>Ideia</th>
                <th>Descrição</th>
                <th>Setor</th>
                <th>Modelo de<br />Receitas</th>
                <th>Conhecimentos<br />Específicos</th>
                <th>Tecnologia<br/>Necessária</th>
                <th>Investimento<br />Inicial</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {ideaList.map((idea, index) => {
                return (
                  <tr key={index} id={index} onClick={handleIdeaClick}>
                    <td>{idea.nome || idea.id}</td>
                    <td data-tip={idea.description}>{idea.description}</td>
                    <td>{idea.setor}</td>
                    <td>Transacional</td>
                    <td>{fieldIntensity(idea.conhecimento)}</td>
                    <td>{fieldIntensity(idea.tecnologia)}</td>
                    <td>{fieldInvestimento(idea.investimento)}</td>
                    <td className="actions">
                      <div className="action">
                        <Like data-idea={idea.id} fill={Colors.green} />
                        <span>{idea.likes}</span>
                      </div>
                      <div className="action">
                        <Like data-idea={idea.id} fill={"red"} className="thumbs-down" />
                        <span>{idea.dislikes}</span>
                      </div>
                      <div className="action">
                        <Star data-idea={idea.id} fill={Colors.orange} />
                        <span>{idea.favorites}</span>
                      </div>
                    </td>
                  </tr>
                )
              })}
            </tbody>
          </table>
        </Table>
      </Content>
    </Container>
  )

}

const mapStateToProps = (state) => ({
  User: state.UserReducer
});
const mapDispatchToProps = (dispatch) => ({
  showAlert: ({title = '', message = '', callback}) => dispatch({type: 'SHOW_ALERT', title: title, message: message, callback: callback}),
  showConfirm: ({title = '', message = '', onYes, onNo}) => dispatch({type: 'SHOW_CONFIRM', title: title, message: message, onYes: onYes, onNo: onNo}),
  loading: (visible) => dispatch({type: (visible ? 'SHOW_LOADING' : 'HIDE_LOADING')})
});

export default connect(mapStateToProps, mapDispatchToProps)(AdminIdeas);