import React from 'react';
import styled from 'styled-components';

import { ReactComponent as Arrow } from './icons/arrow-down.svg'; 

const Container = styled.div`
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 0;
  .label {
    font-size: ${props => 1.6 * props.size}rem;
    font-family: Montserrat-Bold;
  }
  .field{
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    margin-top: 0.5rem;
    svg {
      position: absolute;
      height: ${props => props.size * 1.8}rem;
      left: 1rem;
    }
    select {
      font-size: ${props => props.size * 1.6}rem;
      font-family: Montserrat-Black;
      -webkit-appearance: none;
      -moz-appearance: none;
      width: 100%;
      padding: 1rem 1rem 1rem 4rem;
      border-radius: 5px;
      background-color: ${props => props.bgcolor};
      border: none;
      outline: none;
    }
  }
`

const Select = ({placeholder = 'Selecione', options = [], onChange=() => {}, size = 1, backgroundColor = '#F8F8F8', caption = '', value = ''}) => {
  return (
    <Container size={size} bgcolor={backgroundColor}>
      {caption !== '' &&
        <div className="label">
          <label htmlFor="">{caption}</label>
        </div>
      } 
      <div className="field">
        <Arrow />
        <select onChange={evt => onChange(evt.target.value)} value={value}>
          <option value="" >{placeholder}</option>

          {options.map(item => <option value={item.value}>{item.caption}</option>)}
        </select>
      </div>
    </Container>
  )
}

export default Select;