const INITIAL_STATE = { 
  email: '',
  name: '',
  id: '',
  is_admin: ''
};

const UserReducer = (state = INITIAL_STATE, action) => {
switch (action.type){
  case 'LOGIN_USER':
    return {
      ...state,
      email: action.email,
      name: action.name,
      id: action.id,
      is_admin: action.is_admin
    };
  case 'LOGOUT_USER':
    return {
      ...state,
      email: '',
      name: '',
      id: '',
      is_admin: ''
    };
  default: 
    return state;
    
}
}

export default UserReducer;