import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    h2 {
      font-size: 2.6rem;
    }
    .value {
      background-color: #F8F8F8;
      min-width: 60%;
      min-height: 8rem;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 5px;
      h1 {
        font-size: 3rem;
      }
    }
`


const Counter = ({value, caption}) => {
  return (
    <Container>
      <h2>{caption}</h2>
      <div className="value">
        <h1>{value}</h1>
      </div>
    </Container>
  )
}

export default Counter;