import React, { useRef } from 'react';
import styled from 'styled-components';
import CurrencyInput from 'react-currency-input';

const Container = styled.div`
  width: auto;
  height: auto;
  display: flex;
  flex-direction: column;
`

const Label = styled.label`
  font-size: ${props => 1.6 * props.size}rem;
  font-family: Montserrat-Bold;
`

const TextArea = styled.textarea`
  border: none;
  background: ${props => props.background};
  font-size: ${props => 1.4 * props.size}rem;
  padding: 0.8rem;
  margin-top: 0.5rem;
  outline: none;
  font-family: Montserrat-Medium;
  width: 100%;
  resize: none;
`

const InputContainer = styled.div`
  input {
    border: none;
    background: ${props => props.background};
    font-size: ${props => 1.4 * props.size}rem;
    padding: 0.8rem;
    margin-top: 0.5rem;
    outline: none;
    font-family: Montserrat-Medium;
    width: 100%;
  }
`

const TextInput = ({label = '', type = 'text', value = '', onChange = Function, name = '', disabled = false, multiline= false, size = 1, placeholder = '', background = 'white', max}) => {

  const currencyRef = useRef();

  const handleInputChange = evt => {
    onChange(evt.target.value);
  }
  const handleCurrencyInputChange = evt => {
    onChange(evt.target.value);
  }

  return (
    <Container>
      <Label className="noselect" size={size}>{label}</Label>
      {multiline ? 
        <TextArea name={name} type={type} value={value} onChange={handleInputChange} disabled={disabled} rows="4" size={size} background={background}></TextArea>
        :
        <InputContainer size={size} background={background}>
          {type === 'currency' ? 
            <CurrencyInput ref={currencyRef} value={value} onChangeEvent={handleCurrencyInputChange} decimalSeparator="," thousandSeparator="." prefix="R$ "/>
          :
            <input placeholder={placeholder} name={name} type={type} value={value} onChange={handleInputChange} disabled={disabled} max={max}/>
          }
        </InputContainer>
      }

    </Container>
  )
}

export default TextInput;