import React from 'react';
import { useState } from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';

import AdminUsers from './Users';
import AdminProjects from './Projects';
import AdminIdeas from './Ideas';
import AdminReports from './Reports';
import AdminOverview from './Overview';
import { useEffect } from 'react';
import axios from 'axios';

const Tabs = styled.div`
  min-height: 5rem;
  display: flex;
  margin-bottom: 2rem;
  .tab {
    font-family: Montserrat-Bold;
    font-size: 2.4rem;
    padding: 2rem 1rem 0rem 1rem;
    border-bottom: 3px solid white;
    color: #BBB;
    transition: all 0.5s ease;
    cursor: pointer;
    &.active {
      color: black;
      border-bottom: 3px solid black;
    }
  }
`

const Container = styled.div`
  width: auto; 
  margin: 0rem 10rem;
  a{
    text-decoration: none;
  }
  overflow: hidden;
`

const Admin = () => {

  const [tab, setTab] = useState('overview');

  return (
    <Container>
      <Tabs>
        <div onClick={() => setTab('overview')} className={`tab ${tab === 'overview' ? 'active' : ''}`}>Overview</div>
        <div onClick={() => setTab('ideas')} className={`tab ${tab === 'ideas' ? 'active' : ''}`}>Ideias</div>
        <div onClick={() => setTab('projects')} className={`tab ${tab === 'projects' ? 'active' : ''}`}>Projetos</div>
        <div onClick={() => setTab('reports')} className={`tab ${tab === 'reports' ? 'active' : ''}`}>Relatórios</div>
        <div onClick={() => setTab('users')} className={`tab ${tab === 'users' ? 'active' : ''}`}>Usuários</div>
      </Tabs>
      {tab === 'users' && <AdminUsers setTab={setTab}/>}
      {tab === 'projects' && <AdminProjects setTab={setTab}/>}
      {tab === 'ideas' && <AdminIdeas setTab={setTab}/>}
      {tab === 'reports' && <AdminReports setTab={setTab}/>}
      {tab === 'overview' && <AdminOverview setTab={setTab}/>}
    </Container>
  )
}

export default connect()(Admin);